import styled, { css } from "styled-components";

type Typography = {
  $margin?: "sm" | "md" | "lg" | "none";
  $align?: "left" | "center" | "right";
  $font?: "primary" | "secondary" | "tertiary";
};

type Paragraph = Typography & {
  $size?: "sm" | "md" | "lg";
}

type Heading = Typography;

const typographyStyles = css<Heading>`
  font-family: ${({ $font }) =>
    $font === "secondary" ? '"Audiowide", cursive' : $font === 'tertiary' ? '"Wallpoet", cursive': "inherit"};
  margin: ${({ $margin }) =>
    $margin === "sm"
      ? "0 0 0.5em"
      : $margin === "md"
      ? "0 0 1em"
      : $margin === "lg"
      ? "0 0 1.5em"
      : "0"};
  text-align: ${({ $align }) => $align};
`;

export const H1 = styled.h1<Heading>`
  ${typographyStyles}
`;

export const H2 = styled.h2<Heading>`
  ${typographyStyles}
`;

export const H3 = styled.h3<Heading>`
  ${typographyStyles}
`;

export const H4 = styled.h4<Heading>`
  ${typographyStyles}
`;

export const P = styled.p<Paragraph>`
  ${typographyStyles}
  font-size: ${({ $size }) =>
    $size === "sm"
      ? "0.75rem"
      : $size === "lg"
      ? "1.5rem"
      : "1rem"};
`;

export const Label = styled.label<Paragraph>`
  ${typographyStyles}
  font-size: ${({ $size }) =>
    $size === "sm"
      ? "0.75rem"
      : $size === "lg"
      ? "1.5rem"
      : "1rem"};
`;
