import { Signer, Transaction, BigNumber } from "ethers";

export type Signature = {
  v: number;
  r: string;
  s: string;
};

export type SignatureDetails = {
  title?: string;
  description?: string;
};

export type Connection = {
  isConnected: boolean | null;
  clients: string[];
  isAuthorized: boolean;
};

export type ConnectionStatus = {
  isAuthorized: boolean;
  isAuthorizing: boolean;
  isConnected: boolean;
};

export type RawSale = {
  assetsAndToken: ArrayBuffer;
  seller: ArrayBuffer;
  signature: ArrayBuffer;
  price: ArrayBuffer;
};

export type NewSale = {
  expiry: number;
  assets: BigNumber;
  price: BigNumber;
};

export type Listener = (dataArray: Uint8Array) => void;
// maybe give each message an ID and wait for ACK, not necessary though
// maybe implement if for request-response
export type SendMessage = (data: ArrayBuffer) => void;

export type SaleDetails = NewSale & {
  timestamp: number;
  address: string;
  r: number;
  signature: Uint8Array;
  isUsed?: boolean;
  isVerified?: boolean;
};

export type RzRNodeContext = {
  connectionStatus: {
    isConnected: boolean;
    isAuthorized: boolean;
    isAuthorizing: boolean;
  };
  socket: {
    subscribe: (messageType: number, l: Listener) => void;
    unsubscribe: (messageType: number, l: Listener) => void;
    sendMessage: SendMessage;
  };
  createSale: (sale: NewSale) => void;
};
export type Asset = {};

type NumberLike = string | number | BigNumber;

export type Credits = {
  value: BigNumber;
  assetRequirement: number;
};

export type Account = {
  assets: number[];
  credits: Credits;
};

type TransactionParams = {
  name: string;
  value: string;
};

export type TransactionReceipt = Transaction & {
  blockNumber: number;
};

export type TransactionDetails = {
  name: string;
  params: TransactionParams[];
};

export type Game = {
  token: BigNumber;
  players: string[];
  gameAddress: string;
};

export type RzRContext = {
  account: Account;
  pendingTransaction: TransactionDetails | null;
};

export type ShardElement = {
  sign?: number;
  value?: number;
};

export type Shard = {
  sign: number;
  value: number;
};

export const SHARDS: Shard[] = [
  { sign: 0, value: 4 },
  { sign: 0, value: 3 },
  { sign: 1, value: 4 },
  { sign: 0, value: 2 },
  { sign: 1, value: 3 },
  { sign: 0, value: 1 },
  { sign: 1, value: 2 },
  { sign: 1, value: 1 },
  { sign: 3, value: 4 },
  { sign: 3, value: 3 },
  { sign: 3, value: 2 },
  { sign: 3, value: 1 },
];

// basically we have our own DNS
// signed identity <> address
export type RTCConfig = {
  iceServers: Array<{
    urls: string;
  }>;
};

export type ContractConfig = {
  address: string;
  abi: any;
};

export type Player = {
  address: string;
  identity: string;
  blockNumber: number;
  previousIdentity: string;
};

export type Router = {
  address: string;
  url: string;
};

export type Schema = {
  [key: string]: {
    type:
      | "buffer"
      | "number"
      | "string"
      | "repeating"
      | "hexstring"
      | "bignumber"
      | "boolean";
    byteLength?: number; // if not provided, assuming end of data
    schema?: Schema; // for repeating type
  };
};

export type Primitive = Uint8Array | number | string | BigNumber | boolean;
export type ArrayOfPrimitives = Array<{ [key: string]: Primitive }>;

export type AppliedSchema = {
  [key: string]: Primitive | ArrayOfPrimitives;
};

export type GameData = AppliedSchema & {
  signature: Uint8Array;
  gameData: Uint8Array;
}
