import Observable, { Listener } from "../Observable";
import { buildSchema, applySchema, compressSale, once, timer } from "../utils";
import {
  SaleDetails,
  Router,
  ConnectionStatus,
  Schema,
  AppliedSchema,
} from "../types";
import { utils, Signer } from "ethers";
import {
  PingSchema,
  SignalMessageType,
  messageSchemas,
  MessageSchemas,
  SignalMessageSchema,
  OfferSchema,
  AnswerSchema,
  CandidateSchema,
} from "./messageTypes";

import RzRDB from "../RzRDB";
import Ethereum from "../Ethereum";
import logger from "../Log";
import RouterConnection from "./RouterConnection";

import ConnectionMatrix from "../ConnectionMatrix";

export * from "./messageTypes";

const log = (s: string, slot?: number) => logger.log(s, "RzRSocket", slot);

export type RzRSocketEventMap = MessageSchemas & {
  socketConnectionStatusChanged: ConnectionStatus;
  resetRouters: boolean;
};

type RouterWithSocket = Router & {
  connection: RouterConnection;
};

export enum OperationModes {
  ACTOR,
  LISTENER,
}

export default class RzRSocket extends Observable<RzRSocketEventMap> {
  ethereum: Ethereum;
  rzrDB: RzRDB;

  operationMode: OperationModes;
  connectionMatrix: ConnectionMatrix;

  // the two routers, AAB or ABB -> depending on which was added latest
  routers: [RouterWithSocket | null, RouterWithSocket | null] = [null, null];

  constructor(
    rzrDB: RzRDB,
    ethereum: Ethereum,
    connectionMatrix: ConnectionMatrix,
    operationMode: OperationModes
  ) {
    super();

    this.operationMode = operationMode;

    this.rzrDB = rzrDB;

    this.ethereum = ethereum;

    this.connectionMatrix = connectionMatrix;
  }

  private resetConnection() {
    // TODO: nonono, what if connection is shared with other rzr sockets?
    /*
    this.routers.forEach(
      (router) => router && this.connectionMatrix.releaseRouter(router.address)
    );*/
  }

  reset = () => {
    this.emit("resetRouters", true);
    this.removeAllListeners();
  };

  connect = async (address?: string) => {
    this.resetConnection();

    const routers = await this.rzrDB.loadClosestRouters(
      address || this.ethereum.address
    );

    if (!routers || !this.ethereum.identity) return;

    // now this is where we need to decide what to do based on mode of operation
    //
    // LISTENER => connect to both routers
    // ACTOR => look for one that says that the client is available, if both reject only then concede connection
    // basically, if we have a target peer, we are actors, if we don't we are listeners

    const routerConnection =
      await this.connectionMatrix.getOrCreateRouterConnection(routers[0]);

    this.routers[0] = {
      ...routers[0],
      connection: routerConnection,
    };

    if (!routers[1]) {
      this.routers[1] = {
        ...routers[0],
        connection: routerConnection,
      };
    }

    // connectionMatrix.registerDependency(router)

    this.connectRouter(this.routers[0].connection)

    this.emit("socketConnectionStatusChanged", {
      isAuthorized: true,
      isConnected: true,
      isAuthorizing: false,
    });
  };

  connectRouter(routerConnection: RouterConnection) {
    const redirect =
      <K extends keyof RzRSocketEventMap>(type: K) =>
      (ev: RzRSocketEventMap[K]) =>
        this.emit(type, ev);

    const onPing = redirect(SignalMessageType.PING);
    const onAnswer = redirect(SignalMessageType.ANSWER);
    const onOffer = redirect(SignalMessageType.OFFER);
    const onCandidate = redirect(SignalMessageType.CANDIDATE);
    const onClientUnavailable = redirect(SignalMessageType.CLIENT_UNAVAILABLE);

    routerConnection.addEventListener(SignalMessageType.PING, onPing);
    routerConnection.addEventListener(SignalMessageType.OFFER, onOffer);
    routerConnection.addEventListener(SignalMessageType.ANSWER, onAnswer);
    routerConnection.addEventListener(SignalMessageType.CANDIDATE, onCandidate);

    routerConnection.addEventListener(
      SignalMessageType.CLIENT_UNAVAILABLE,
      onClientUnavailable
    );

    once(this, "resetRouters").then(() => {
      routerConnection.removeEventListener(SignalMessageType.PING, onPing);
      routerConnection.removeEventListener(SignalMessageType.OFFER, onOffer);
      routerConnection.removeEventListener(SignalMessageType.ANSWER, onAnswer);
      routerConnection.removeEventListener(
        SignalMessageType.CANDIDATE,
        onCandidate
      );
      routerConnection.removeEventListener(
        SignalMessageType.CLIENT_UNAVAILABLE,
        onClientUnavailable
      );
    });
  }

  send<K extends keyof MessageSchemas>(
    messageType: K,
    data: MessageSchemas[K]
  ) {
    if (this.routers[0] === null) throw new Error("router broken");
    return this.routers[0].connection.socket.send(
      utils.concat([
        new Uint8Array([messageType]),
        new Uint8Array(buildSchema(messageSchemas[messageType], data)),
      ])
    );
  }
}

export { RouterConnection };
