import { BigNumber, utils } from "ethers";
import { Shard, SHARDS } from "./types";
import Observable from "./Observable";
import { Schema, AppliedSchema, SaleDetails } from "./types";

export const ec = new TextEncoder();
export const dc = new TextDecoder();

export class ByteLengthViolation extends Error {
  constructor(message: string) {
    super(message);
    this.name = "ByteLengthViolation";
  }
}

export const numberToTimeString = (n: number): string => {
  if (n < 0) return "0m 0s";
  const milliseconds = n % 1000;
  n -= milliseconds;
  n /= 1000;
  const seconds = n % 60;
  n -= seconds;
  if (n === 0) {
    return `0m ${seconds}s`;
  }
  n /= 60;
  const minutes = n % 60;
  n -= minutes;
  if (n === 0) {
    return `${minutes}m ${seconds}s`;
  }
  n /= 60;
  const hours = n % 60;
  n -= hours;
  if (n === 0) {
    return `${hours}h ${minutes}m`;
  }
  n /= 60;
  const days = n % 24;
  return `${days}d ${hours}h ${minutes}m`;
};

export const formatAddress = (address: string, n: number = 3): string =>
  `${address.slice(0, 2 + n)}...${address.slice(-n)}`;

export const formatHex = (address: string, n: number = 3): string =>
  `${address.slice(0, 2 + n)}...${address.slice(-n)}`;

export const timer = (timeout: number): Promise<undefined> =>
  new Promise((r) => setTimeout(r, timeout));

export const watch = async <T>(
  watcher: () => T | null | Promise<T | null>,
  t = 250,
  maxRetries = 1000
): Promise<T> => {
  const value = await watcher();

  if (value !== null) {
    return value;
  }

  if (maxRetries === 0) throw new Error("failed");

  await timer(t * 1.1);

  return watch(watcher, t, maxRetries - 1);
};

export const bytesToNumber = (i: Uint8Array): number =>
  parseInt(utils.hexlify(i));

export const getShardIndex = (shard: Shard) =>
  SHARDS.findIndex((s) => s.sign === shard.sign && s.value === shard.value);

export const getShardAsAsset = (shard: Shard) =>
  BigNumber.from("1").shl(getShardIndex(shard) * 8);

export const digestAssetsAndToken = (data: Uint8Array) => ({
  expiry: bytesToNumber(data.slice(0, 2)),
  timestamp: bytesToNumber(data.slice(2, 10)),
  r: bytesToNumber(data.slice(10, 12)),
  assets: BigNumber.from(data.slice(12, 32)),
});

export const digestSale = (sale: Uint8Array) => ({
  ...digestAssetsAndToken(sale),
  price: BigNumber.from(sale.slice(32, 64)),
  signature: sale.slice(64, 129),
  address: utils.hexlify(sale.slice(129, 149)),
});

export const compressSale = (sale: SaleDetails) => {
  const assetsAndToken = utils.concat([
    utils.zeroPad(utils.arrayify(sale.expiry), 2),
    utils.zeroPad(utils.arrayify(sale.timestamp), 8),
    utils.zeroPad(utils.arrayify(sale.r), 2),
    utils.zeroPad(utils.arrayify(sale.assets), 20),
  ]);

  const priceBytes = utils.zeroPad(utils.arrayify(sale.price), 32);

  return utils.concat([
    assetsAndToken,
    priceBytes,
    utils.arrayify(sale.signature),
    utils.arrayify(sale.address),
  ]);
};

export const getDistance = (a: Uint8Array, b: Uint8Array): number => {
  let sum = 0;

  for (let i = 0; i < a.length; ++i) {
    sum += Math.abs(a[0] - b[0]) << (a.length - i);
  }

  return sum;
};

export const findClosestAddress = <T extends { address: string }>(
  targetAddress: string,
  entitiesWithAddress: T[],
  lowestBits = true
): T => {
  const toComparable = (address: string) =>
    utils.arrayify(targetAddress).slice(...(lowestBits ? [10, 20] : [0, 10]));

  const toCompare = toComparable(targetAddress);

  let minDistance = 1 << 11;
  let minEntityWithAddress = entitiesWithAddress[0];

  entitiesWithAddress.forEach((entityWithAddress) => {
    const d = getDistance(toCompare, toComparable(entityWithAddress.address));
    if (d < minDistance) {
      minDistance = d;
      minEntityWithAddress = entityWithAddress;
    }
  });

  return minEntityWithAddress;
};

export function validateByteLength(
  data: ArrayBuffer,
  targetLength: number,
  minOnly = false
): void {
  if (minOnly && data.byteLength < targetLength) return;
  if (data.byteLength !== targetLength)
    throw new ByteLengthViolation(
      `data byte length verification falied. Target byte length: ${targetLength} Received byte length: ${data.byteLength}`
    );
}

export function splitByLength(
  length: number,
  data: ArrayBuffer
): ArrayBuffer[] {
  const dataArray = [];

  let i = 0;
  do {
    const d = data.slice(i * length, (i + 1) * length);
    if (d.byteLength) dataArray.push(d);
  } while (++i * length < data.byteLength);

  return dataArray;
}

export function applySchema<T extends AppliedSchema>(
  schema: Schema,
  data: ArrayBuffer
): T {
  const result: AppliedSchema = {};
  let needle = 0;

  for (const key in schema) {
    const s = schema[key];

    const dataEnd =
      s.type === "boolean"
        ? needle + 1
        : s.byteLength
        ? needle + s.byteLength
        : data.byteLength;

    const value = data.slice(needle, dataEnd);

    if (s.type === "boolean") {
      result[key] = !!new Uint8Array(value)[0];
    } else if (s.type === "string") {
      result[key] = dc.decode(value);
    } else if (s.type === "number" && s.byteLength) {
      result[key] = parseInt(BigNumber.from(value).toString());
    } else if (s.type === "buffer") {
      result[key] = new Uint8Array(value);
    } else if (s.type === "hexstring") {
      result[key] = utils.hexlify(new Uint8Array(value));
    } else if (s.type === "bignumber") {
      result[key] = BigNumber.from(new Uint8Array(value));
    } else if (s.type === "repeating") {
      result[key] = [];
      if (!s.schema) throw new Error(`no schema specified for ${key}`);
      while (needle < data.byteLength) {
        const fullByteLength = Object.keys(s.schema).reduce(
          (sum, k) => sum + ((s.schema as Schema)[k].byteLength as number),
          0
        );
        const end = needle + fullByteLength;
        (result[key] as Array<AppliedSchema>).push(
          applySchema(s.schema, data.slice(needle, end))
        );
        needle = end;
      }
    }

    needle = dataEnd;
  }

  return result as T;
}

export function buildSchema(schema: Schema, data: AppliedSchema): ArrayBuffer {
  let result = new Uint8Array();

  for (const key in schema) {
    if (!(key in data)) throw new Error(`Missing data: ${key}`);
    const s = schema[key];
    const d = data[key];

    if (s.type === "boolean") {
      result = utils.concat([result, new Uint8Array([d ? 1 : 0])]);
    } else if (s.type === "string") {
      result = utils.concat([result, ec.encode(d as string)]);
    } else if (s.type === "number" && s.byteLength) {
      if (typeof d !== "number") {
        throw new Error(`Wrong datatype for ${key}. Expected number.`);
      }

      // not sure why strict type is required
      result = utils.concat([
        result,
        utils.zeroPad(utils.arrayify(d as number), s.byteLength),
      ]);
    } else if (s.type === "bignumber" && s.byteLength) {
      // not sure why strict type is required
      result = utils.concat([
        result,
        utils.zeroPad(utils.arrayify(d as BigNumber), s.byteLength),
      ]);
    } else if (s.type === "buffer" || s.type === "hexstring") {
      const _d = utils.arrayify(d as string | Uint8Array);

      // arraybuffer type, check for byte length
      if (s.byteLength) validateByteLength(_d.buffer, s.byteLength);

      result = utils.concat([result, _d]);
    } else if (s.type === "repeating") {
      result = utils.concat([
        result,
        utils.concat(
          (d as Array<AppliedSchema>).map(
            (_d) => new Uint8Array(buildSchema(s.schema as Schema, _d))
          )
        ),
      ]);
    } else {
      throw new Error(`Unprocessable schema type: ${s.type}`);
    }

    delete data[key];
  }

  if (Object.keys(data).length) {
    throw new Error(`invalid keys supplied to schema: ${Object.keys(data)}`);
  }

  return result.buffer;
}

// can be improved to actually return the values...
export const sequentialProcess = <T>(
  inputs: T[],
  f: (event: T) => Promise<any>
) =>
  inputs.reduce(
    (previousPromise: Promise<any>, input) =>
      previousPromise.then(() => f(input)),
    Promise.resolve(true)
  );

export const once = <EventMap, K extends keyof EventMap>(
  module: Observable<EventMap>,
  event: K,
  config?: {
    precondition?: (result: EventMap[K]) => boolean;
    timeoutIn?: number;
    silent?: boolean;
  }
) =>
  new Promise<EventMap[K]>((resolve, reject) => {
    let resolved = false;
    const resolveAndFulfil = (result: EventMap[K]) => {
      if (config?.precondition && !config.precondition(result)) return false;

      resolve(result);

      resolved = true;

      return true;
    };

    module.addEventListener(event, resolveAndFulfil);

    if (config?.timeoutIn) {
      timer(config.timeoutIn).then(() => {
        if (!resolved) {
          module.removeEventListener(event, resolveAndFulfil);
          if (config?.silent !== true) reject();
        }
      });
    }
  });

