import styled from "styled-components";
import Asset, { styles, selectedStyles } from "../../lvl1/Asset";
import Board, { Element } from "../../lvl2/Board";
import { StyledTriangle } from "../../lvl1/Triangle";
import { Shard } from "../../../rzr";

const AssetsBoardContainer = styled.div`
  overflow-x: auto;
  --triangle-size: 14vw;
  --triangle-margin-offset: -7vw;
  ${StyledTriangle} {
    background: #222222;
  }

  > div {
    font-size: 3vw;
    @media screen and (min-width: 720px) {
      font-size: 1rem;
      --triangle-margin-offset: -2rem;
      --triangle-size: 5rem;
    }
  }
`;

const AssetSelector: React.FC<{
  selectedIndices?: number[];
  onSelect: (i: number, shard: Shard) => void;
  onDeselect?: (i: number) => void;
  assets: Shard[];
}> = ({ assets, selectedIndices = [], onSelect, onDeselect }) => {
  const cols = assets.length + ((assets.length & 1) === 0 ? 1 : 2);
  return (
    <AssetsBoardContainer>
      <Board
        responsiveRows={[3, 3, 3]}
        responsiveCols={[cols, cols, cols]}
        elements={assets.map(({ sign, value }, i) => {
          const isSelected = selectedIndices.includes(i);
          return {
            position: [
              [i, i % 2],
              [i, i % 2],
              [i, i % 2],
            ],
            styles: isSelected ? selectedStyles : styles,
            content: <Asset sign={sign} value={value} />,
            action: () => {
              if (!isSelected) {
                onSelect(i, assets[i]);
              } else if (onDeselect) {
                onDeselect(i);
              }
            },
          } as Element;
        })}
            gap={4}
      />
    </AssetsBoardContainer>
  );
};

export default AssetSelector;
