import Observable, { Listener } from "../Observable";
import { BigNumber, utils, Wallet } from "ethers";
import { Schema, GameData, SaleDetails, Game, Player, Router } from "../types";
import Database, { SerializedSale, RawGame } from "./backends/database";
import logger from "../Log";
import {
  applySchema,
  buildSchema,
  findClosestAddress,
  digestAssetsAndToken,
} from "../utils";
const log = (s: string, slot?: number) => logger.log(s, "RzRDB", slot);
const _log = logger._log("RzRDB");

// must be 2 bytes max for now
const IDENTITY_DEPTH = 65536;

export enum DataType {
  SALE = 0,
  GAME,
  SIGNATURE,
}

export type RzRDBEventMap = {
  newPlayerStored: Player;
  databaseLoaded: boolean;
  [DataType.SALE]: SaleDetails | null;
  [DataType.GAME]: Uint8Array;
};

type IdentityChain = {
  latestIdentityIndex: number;
  identityRoot: Uint8Array;
};

const parseSale = (sale: SerializedSale): SaleDetails => ({
  assets: BigNumber.from(sale.assets),
  price: BigNumber.from(sale.price),
  signature: utils.arrayify(sale.signature),
  timestamp: sale.timestamp,
  expiry: sale.expiry,
  r: sale.r,
  address: sale.address,
  isVerified: sale.isVerified,
});

const serializeSale = (sale: SaleDetails): SerializedSale => {
  const assetsAndToken = utils.concat([
    utils.zeroPad(utils.arrayify(sale.expiry), 2),
    utils.zeroPad(utils.arrayify(sale.timestamp), 8),
    utils.zeroPad(utils.arrayify(sale.r), 2),
    utils.zeroPad(utils.arrayify(sale.assets), 20),
  ]);

  const priceBytes = utils.zeroPad(utils.arrayify(sale.price), 32);

  const identity = utils
    .verifyMessage(utils.concat([assetsAndToken, priceBytes]), sale.signature)
    .toLowerCase();

  return {
    ...sale,
    identity,
    assets: utils.hexlify(sale.assets),
    price: utils.hexlify(sale.price),
    signature: utils.hexlify(sale.signature),
    isUsed: sale.isUsed ? true : false,
    isVerified: sale.isVerified ? true : false,
  };
};

const gameDataSchema: Schema = {
  signature: {
    type: "buffer",
    byteLength: 65,
  },
  gameData: {
    type: "buffer",
  },
};

export const getExpiry = (sale: {
  expiry: number;
  timestamp: number;
}): number => {
  return sale.expiry * 60 + sale.timestamp;
};

export default class RzRDB extends Observable<RzRDBEventMap> {
  loadedIdentitiesCache = new Map<string, Uint8Array>();
  backend: Database;

  constructor(Backend: typeof Database) {
    super();

    this.backend = new Backend();

    this.backend.addEventListener("databaseLoaded", () =>
      this.emit("databaseLoaded", true)
    );
  }

  initialize() {
    // TODO: remove all sales that do not belong to an active identity
    // this may be more difficult than originally thought, but certainly necessary
    // actually it'll be fine, identities expire more rarely than sales

    return _log(
      "purging expired sales",
      this.backend
        .initialize()
        .then(() => this.backend.purgeExpiredSales(new Date().getTime() / 1000))
    );
  }

  async flagSaleAsUsed(assetsAndToken: Uint8Array) {
    const slot = log("flagging sale as used...");

    const expiry = getExpiry(digestAssetsAndToken(assetsAndToken));

    const sale = await this.loadSale(expiry);

    if (sale === null) {
      return log("done", slot);
    }

    try {
      await this.storeSale({ ...sale, isUsed: true });

      log("done", slot);

      this.emit(DataType.SALE, null);
    } catch (e) {
      log("failed", slot);
    }
  }

  loadLatestBlock() {
    return this.backend.loadLatestBlock();
  }

  async storeLatestBlock(blockNumber: number): Promise<number> {
    await this.backend.storeLatestBlock(blockNumber);
    _log(
      `upgrading local latest block to ${blockNumber}`,
      Promise.all([
        this.backend.purgeExpiredEvents(blockNumber),
        this.purgeExpiredPlayers(blockNumber - 1000000),
      ])
    );
    return blockNumber;
  }

  async storeRouter(router: Router) {
    const slot = log("storing router...");

    await this.backend.storeRouter(router);

    log("done", slot);
  }

  async storeOrDeleteGame(token: string, player: string, gameAddress: string) {
    // load existing identity chains, store if has any..
    const [identities, existingGame] = await Promise.all([
      this.backend.loadIdentities(),
      this.backend.loadGame(token),
    ]);
    let slot;
    let newGame: RawGame | null = null;
    if (!existingGame) {
      slot = log(`storing ${token}...`);
      newGame = {
        token,
        players: [player.toLowerCase()],
        gameAddress,
      };
    } else if (
      identities.includes(player) ||
      identities.includes(existingGame.players[0])
    ) {
      // TODO: check for player equation... might receive event twice
      slot = log(`updating ${token}...`);
      newGame = {
        token,
        players: [existingGame.players[0], player.toLowerCase()],
        gameAddress,
      };
    }

    if (newGame === null) {
      slot = log(`removing ${token}... `);
      await this.backend.removeGame(token);
    } else {
      await this.backend.storeGame(newGame);
      log("done", slot);
    }

    this.emit(DataType.GAME, new Uint8Array());
  }

  async loadGames(): Promise<{
    signatures: string[];
    games: Game[];
  }> {
    const slot = log("loading games...");

    const [signatures, games] = await Promise.all([
      this.backend.loadSignatures(),
      this.backend.loadGames(),
    ]);

    log("done", slot);

    return {
      signatures,
      games: games.map((game: RawGame) => ({
        ...game,
        token: BigNumber.from(game.token),
      })),
    };
  }

  async loadSignature(token: string): Promise<Uint8Array | null> {
    // const slot = log(`verifying sale ${token}...`);
    const signature = await this.backend.loadSignature(token);
    if (!signature) return null;
    return new Uint8Array(signature);
  }

  async loadSale(token: number): Promise<SaleDetails | null> {
    // const slot = log(`verifying sale ${token}...`);
    const sale = await this.backend.loadSale(token);
    // log("done", slot);

    if (!sale) return null;

    return parseSale(sale);
  }

  async loadSales(): Promise<SaleDetails[]> {
    const slot = log("loading sales...");

    const sales = await this.backend.loadSales();

    log("done", slot);

    return sales.filter((sale) => !sale.isUsed).map(parseSale);
  }

  async loadRouters(): Promise<Router[]> {
    const slot = log("loading sales...");

    const routers = await this.backend.loadRouters();

    log("done", slot);

    return routers;
  }

  async loadRouter(address: string): Promise<Router | null> {
    const slot = log(`loading router ${address}...`);

    const router = await this.backend.loadRouter(address);

    if (!router) {
      log("failed", slot);
      return null;
    }

    log("done", slot);

    return router;
  }

  async storeNextIdentityAddress(address: string): Promise<Wallet> {
    const slot = log("loading next identity...");

    let identityChain = (await this.loadIdentityChain(address)) || {
      identityRoot: utils.randomBytes(32),
      latestIdentityIndex: IDENTITY_DEPTH,
    };

    const player = await this.loadPlayer(address);

    let privateKey = utils.keccak256(identityChain.identityRoot),
      previousKey = "";

    // should also store index
    let i = 0;
    while (i < identityChain.latestIdentityIndex) {
      previousKey = privateKey;
      privateKey = utils.keccak256(privateKey);
      ++i;
    }

    log(`done (iteration count: ${i})`, slot);

    if (
      player &&
      player.identity !== new Wallet(privateKey).address.toLowerCase()
    ) {
      // this here might be salvagable, it's possible that the previous iteration did not make it to chain..
      // or better yet, we can return the confirmation function
      // ^^ this one is pretty cool
      // actually can sometimes skip numbers in sequence for security
      // so that 65536 - new identites != actual identity
      throw new Error("wallet does not match identity requirements");
    }

    await this.backend.storeIdentity(
      address,
      utils.concat([
        utils.zeroPad(utils.arrayify(identityChain.latestIdentityIndex - 1), 2),
        identityChain.identityRoot,
      ]).buffer
    );

    return new Wallet(previousKey);
  }

  /*
   * loads an identity chain based on any address on the chain
   * */
  async loadIdentityChain(
    address: string
  ): Promise<IdentityChain | null> {
    const identityAndIndex = await this.backend.loadIdentityChain(address);

    if (!identityAndIndex) {
      return null;
    }

    return {
      identityRoot: new Uint8Array(identityAndIndex.slice(2)),
      latestIdentityIndex: parseInt(
        BigNumber.from(new Uint8Array(identityAndIndex.slice(0, 2))).toString(),
        10
      ),
    };
  }

  async loadIdentity(
    address: string,
    identityChainAddress: string
  ): Promise<Wallet | null> {
    const slot = log(`loading identity ${address}...`);

    const cacheEntry = this.loadedIdentitiesCache.get(address);

    if (cacheEntry) {
      log("done (loaded from cache)", slot);
      return new Wallet(cacheEntry);
    }

    const identityChain = await this.loadIdentityChain(identityChainAddress);

    if (!identityChain) { 
      log('failed', slot)
      throw new Error("no identity chain set");
    }

    let privateKey: string = utils.keccak256(identityChain.identityRoot);

    let i;

    // this can be used to recover up to 100 new identities, in case they were generated on another device/browser than this one
    const recoveryKeys = [];

    for (i = 0; i < identityChain.latestIdentityIndex; ++i) {
      privateKey = utils.keccak256(privateKey);

      if (i > identityChain.latestIdentityIndex - 100) {
        recoveryKeys.push(privateKey);
      }
    }

    if (new Wallet(privateKey).address.toLowerCase() !== address) {
      // try and recover identity

      // now it may happen, that the address is actually further on chain
      //
      // TODO: the below works, but should be refactored

      privateKey = utils.keccak256(privateKey);
      if (new Wallet(privateKey).address.toLowerCase() === address) {
        log("identity recovered");
        await this.backend.storeIdentity(
          identityChainAddress,
          utils.concat([
            utils.zeroPad(utils.arrayify(i + 1), 2),
            identityChain.identityRoot,
          ]).buffer
        );
      } else {
        for (let key of recoveryKeys.reverse()) {
          --i;
          if (new Wallet(key).address.toLowerCase() === address) {
            await this.backend.storeIdentity(
              identityChainAddress,
              utils.concat([
                utils.zeroPad(utils.arrayify(i), 2),
                identityChain.identityRoot,
              ]).buffer
            );
            log("identity recovered");
            log("done", slot);
            this.loadedIdentitiesCache.set(address, utils.arrayify(key));
            return new Wallet(key);
          }
        }
        log("failed", slot);
        return null;
      }
    }

    log("done", slot);
    this.loadedIdentitiesCache.set(address, utils.arrayify(privateKey));
    return new Wallet(privateKey);
  }

  async loadGame(token: string): Promise<Game | null> {
    const slot = log(`loading game ${token}...`);

    const game = await this.backend.loadGame(token);

    if (!game) {
      log("failed", slot);
      return null;
    }

    log("done", slot);

    return {
      ...game,
      token: BigNumber.from(game.token),
    };
  }

  async loadGameData(token: string): Promise<GameData | null> {
    const slot = log(`loading game data ${token}...`);

    const gameData = await this.backend.loadGameData(token);

    if (!gameData) {
      log("no game found", slot);
      return null;
    }

    log("done", slot);

    return applySchema(gameDataSchema, gameData) as GameData;
  }

  async storeSignature(token: string, signature: Uint8Array) {
    const slot = log("storing incoming signature...");

    await this.backend.storeSignature(token, signature.buffer);

    this.emit(DataType.GAME, new Uint8Array());

    log("done", slot);
  }

  async storeGameData(token: string, game: GameData) {
    const slot = log("storing game data...");

    await this.backend.storeGameData(token, buildSchema(gameDataSchema, game));

    log("done", slot);
  }

  async storeSale(sale: SaleDetails, isVerified?: boolean) {
    await _log(
      "storing new sale",
      this.backend.storeSale(
        getExpiry(sale),
        serializeSale({ ...sale, isVerified })
      )
    );

    this.emit(DataType.SALE, sale);

    return sale;
  }

  /*
   * only if there is no identity for this address yet 
   * */
  async storeIdentityChain(address: string, identity: Uint8Array): Promise<boolean> {
    const slot = log('storing new identity...')

    const identityChain = (await this.loadIdentityChain(address)) 

    if (identityChain !== null) {
      log('failed', slot)
      return false
    }

    await this.backend.storeIdentity(
      address,
      identity.buffer
    );

    log('done', slot)

    return true;
  }

  async storeEvent(blockNumber: number, transactionHash: string) {
    return _log(
      "storing processed event",
      this.backend.storeEvent(blockNumber, transactionHash)
    );
  }

  async hasEventBeenProcessed(transactionHash: string) {
    return this.backend.hasEventBeenProcessed(transactionHash);
  }

  async storePlayer(player: Player) {
    const existingPlayer = await this.loadPlayer(player.address);

    if (existingPlayer && existingPlayer.identity !== player.identity) {
      // TODO: also do all sorts of expiry magic
      await this.backend.removeAllSalesByIdentity(existingPlayer.identity);
    }

    await _log("storing new player", this.backend.storePlayer(player));

    this.emit("newPlayerStored", player);

    return player;
  }

  async loadPlayer(address: string) {
    const slot = log(`loading player ${address}...`);

    const player = await this.backend.loadPlayer(address);

    if (!player) {
      log("failed", slot);
      return null;
    }

    log("done", slot);

    return player;
  }

  async loadPlayerByIdentity(identity: string) {
    const slot = log(`loading player ${identity}...`);

    const player = await this.backend.loadPlayerByIdentity(identity);

    if (!player) {
      log("failed", slot);
      return null;
    }

    log("done", slot);

    return player;
  }

  async loadAllSalesAssets(address: string): Promise<number[]> {
    return Array.from(
      utils
        .arrayify(
          (await this.backend.loadAllSalesFromAddress(address)).reduce(
            (s: BigNumber, sale: SerializedSale) =>
              s.add(parseSale(sale).assets),
            BigNumber.from("0")
          )
        )
        .reverse()
    );
  }

  async removeGameAndSignatures(token: string) {
    await _log(
      "removing expired signature",
      Promise.all([
        this.backend.removeSignature(token),
        this.backend.removeGame(token),
        this.backend.removeGameData(token),
      ])
    );

    // only should be called once verified that it's been claimed

    this.emit(DataType.GAME, new Uint8Array());
  }

  purgeExpiredPlayers(blockLimit: number) {
    return _log(
      "purging expired players",
      this.backend.purgeExpiredPlayers(blockLimit)
    );
  }

  async loadClosestRouters(
    addressToCompare: string
  ): Promise<null | [Router, Router | null]> {
    const slot = log("loading router configuration...");

    const routers = await this.backend.loadRouters();

    const router1 = findClosestAddress<Router>(addressToCompare, routers, true);
    const router2 = findClosestAddress<Router>(
      addressToCompare,
      routers,
      false
    );

    if (!router1) {
      log("no routers found", slot);
      return null;
    }

    log("done", slot);

    return [router1, router2.address !== router1.address ? router2 : null];
  }

  async loadRandomPlayer(exceptions: Set<string>): Promise<Player | null> {
    const getRandomPlayerThatsNotException = (
      players: Player[]
    ): Player | null => {
      if (exceptions.size >= players.length) return null;

      const player = players[Math.floor(Math.random() * players.length)];

      if (player && !exceptions.has(player.address)) return player;

      return getRandomPlayerThatsNotException(players);
    };

    // TODO: wooahh, loadPlayers() ? seriously, load all players into memory? are you insane?
    return _log(
      "loading random player for sync",
      this.backend.loadPlayers().then(getRandomPlayerThatsNotException)
      // player => player ? `done (${player.address})` : `done, no player found`
    );
  }
}
