import Observable, { Listener } from "./Observable";
import { timer } from "./utils";

export type LogEventMap = {
  logs: string[];
};

class Log extends Observable<LogEventMap> {
  messages: string[] = [];
  listeners: { [K in keyof LogEventMap]: Set<Listener<LogEventMap[K]>> } = {
    logs: new Set<Listener<string[]>>(),
  };

  _log(context: string) {
    return <T>(
      message: string,
      p: Promise<T>,
      {
        successMessage,
        failMessage,
      }: {
        successMessage: string | ((result: T) => string);
        failMessage: string;
      } = {
        successMessage: "done",
        failMessage: "failed",
      }
    ): Promise<T> => {
      const slot = this.log(`${message}...`, context);

      return Promise.all([p, timer(250)])
        .then(([result]) => {
          this.log(
            typeof successMessage === "function"
              ? successMessage(result)
              : successMessage,
            context,
            slot
          );
          return result;
        })
        .catch((e) => {
          this.log(failMessage, context, slot);
          throw e;
        });
    };
  }

  log(message: string, context: string, slot: number = -1): number {
    let index = -1;
    if (slot >= 0) {
      this.messages[slot] = `${this.messages[slot]} ${message}`;
    } else {
      index = this.messages.push(`[${context}]: ${message}`) - 1;
    }

    this.emit("logs", this.messages.slice(-32));

    return index;
  }

  resetLog() {
    this.messages = [];
  }
}

const logger = new Log();
export default logger;
