import styled, { css } from "styled-components";
import { Link } from "react-router-dom";

interface IAction {
  $primary?: boolean;
  $size?: "sm" | "md" | "lg";
}

const actionStyles = css<IAction>`
  ${({ $size }) =>
    $size === "sm"
      ? css`
          font-size: 0.75rem;
        `
      : $size === "lg"
      ? css`
          font-size: 1.5rem;
        `
      : css`
          font-size: 1rem;
        `}
  min-width: 6em;
  padding: 1em;
  font-family: "Audiowide", cursive;
  height: 3rem;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2;
  background: none;
  outline: none;
  border: none;
  color: white;

  &:not([disabled]):hover {
    cursor: pointer;
  }
  &:not([disabled]):focus::after,
  &:not([disabled]):hover::after {
    clip-path: polygon(0% 100%, 0% 100%, 100% 100%, 100% 2%);
  }
  &:not([disabled]):active {
    color: ${({ $primary }) => ($primary ? "orange" : "#22acde")};
  }

  &:not([disabled])::after {
    clip-path: polygon(-30% 50%, 50% 160%, 130% 50%, 50% -60%);
  }
  &:disabled::after {
    clip-path: polygon(0% 100%, 0% 0%, 100% 0%, 100% 100%);
  }
  ::after {
    transition: clip-path 0.25s;
    z-index: -1;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    border: 2px solid ${({ $primary }) => ($primary ? "orange" : "#22acde")};
    content: " ";
  }

  &:disabled {
    color: gray;
  }
  &:disabled::after {
    border: 2px solid grey;
  }
`;

export const ActionButton = styled.button<IAction>`
  ${actionStyles};
`;

export const ActionLink = styled(Link)<IAction>`
  ${actionStyles};
  text-decoration: none;
`;

export const ActionExternalLink = styled.a<IAction>`
  ${actionStyles};
  text-decoration: none;
  `;
