import React from "react";
import styled, {
  FlattenSimpleInterpolation,
  css,
  keyframes,
} from "styled-components";
import type { Element } from "../../lvl2/Board";

interface ITriangle {
  element?: Element;
}

type WithStyles = {
  hasAction?: boolean;
  styles?: string | FlattenSimpleInterpolation;
};

export const triangleDimensions = css`
  min-width: calc(var(--triangle-size) * 1.1547005383793);
  min-height: var(--triangle-size);
  max-width: calc(var(--triangle-size) * 1.1547005383793);
  max-height: var(--triangle-size);
`

export const StyledTriangle = styled.div<WithStyles>`
${triangleDimensions};
  clip-path: polygon(50% 0, 100% 100%, 0 100%);
  background: rgba(10, 10, 10, 0.0);
  z-index: ${({ hasAction }) => (hasAction ? 0 : -1)};
  cursor: ${({ hasAction }) => (hasAction ? "pointer" : "normal")};

  ${({ styles }) =>
    styles
      ? css`
          ${styles}
        `
      : ""};
`;

export const actionStyles = css`
  background: rgba(30, 75, 150, 0.4);
`;

const Container = styled.div<WithStyles>`
  position: relative;
  min-width: var(--triangle-size);
  min-height: var(--triangle-size);
  > div {
    position: absolute;
    ${({ styles }) =>
      styles
        ? css`
            ${styles}
          `
        : ""};
  }
`;

const Button = styled.button`
  display: none;
`;

const Triangle: React.FC<ITriangle> = ({ element }) => {
  if (element) {
    if (element.action) {
      return (
        <StyledTriangle as="label" styles={element.styles} hasAction>
          <Button onClick={element.action} />
          {element.content && element.content}
        </StyledTriangle>
      );
    }

    if (element.isFullSize) {
      return (
        <Container styles={element.styles}>
          <div>{element.content}</div>
        </Container>
      );
    }

    return (
      <StyledTriangle styles={element.styles}>
        {element.content && element.content}
      </StyledTriangle>
    );
  }
  return <StyledTriangle />;
};

export default Triangle;
