import { useContext } from "react";
import RzR from "../contexts/RzR";
import { Shard, SHARDS } from "../rzr";

const useRzRShards = (): { shards: Shard[] } => {
  const {
    account: { assets },
  } = useContext(RzR);

  const shards = assets.reduce(
    (_shards: Shard[], amount, index) => [
      ..._shards,
      ...[...new Array(amount)].map(() => SHARDS[index]),
    ],
    []
  );

  return { shards };
};

export default useRzRShards;
