import Observable, { Listener } from "../../Observable";
import RzRSocket, { SignalMessageType, AnswerSchema } from "../../RzRSocket";
import Ethereum from "../../Ethereum";
import { utils, Wallet } from "ethers";
import { bytesToNumber, once } from "../../utils";
import logger from "../../Log";
const _log = logger._log("Protocol");

const TOKEN_BYTE_LENGTH = 24;

export type CommEventMap = {
  connectionStatusChanged: boolean;
  newMessage: ArrayBuffer;
};

/*
 * Takes care of authentication and provides an interface
 * for further connection negotiation
 * might lift authentication above
 * */
export default class Comm extends Observable<CommEventMap> {
  listeners = {
    connectionStatusChanged: new Set<Listener<boolean>>(),
    newMessage: new Set<Listener<ArrayBuffer>>(),
  };

  isConnecting = false;
  identity: string;
  rzrSocket: RzRSocket;
  ethereum: Ethereum;
  isConnected: boolean;
  token = new Uint8Array();
  protocolId = 0b00;

  constructor(rzrSocket: RzRSocket, ethereum: Ethereum, identity: string) {
    super();
    this.isConnected = false;
    this.identity = identity;
    this.rzrSocket = rzrSocket;
    this.ethereum = ethereum;
  }

  closeConnection() {}
  protected createOffer(): Promise<Uint8Array> {
    return Promise.resolve(new Uint8Array());
  }

  protected processAnswer = async (answer: Uint8Array) => {};
  protected processOffer = async (offer: Uint8Array): Promise<Uint8Array> => {
    return new Uint8Array();
  };

  private verifySignature(signature: string | Uint8Array) {
    return (
      this.identity ===
      utils.verifyMessage(this.token, signature).toLowerCase()
    );
  }

  private requestSignature = (): Promise<string> => 
    this.ethereum.requestSignature(
      this.token,
      {
        title: "authentication",
        description: "to access a rzr peer",
      },
    );

  reset() {
    console.log("reset");
    this.isConnected = false;
    this.emit("connectionStatusChanged", false);
    this.removeAllListeners();
  }

  attachOffer(signature: string, offer: Uint8Array) {
    if (!this.verifySignature(signature)) {
      throw new Error("signature verification failed");
    }

    return _log(
      "validating identity",
      Promise.all([this.processOffer(offer), this.requestSignature()]).then(
        ([answer, signature]) =>
          this.rzrSocket.send(SignalMessageType.ANSWER, {
            identity: this.identity,
            signature,
            answer,
          })
      )
    );
  }

  // not the best terminology it's more like initate connection
  connect() {
    this.isConnecting = true;
    _log(
      `initiating connection using protocol ${this.protocolId}`,
      Promise.all([this.createOffer(), this.requestSignature()]).then(
        ([offerData, signature]) => {
          this.rzrSocket.send(SignalMessageType.OFFER, {
            identity: this.identity,
            protocolId: this.protocolId,
            signature,
            requestedIdentity: this.identity,
            offerData,
          });

          return once(this.rzrSocket, SignalMessageType.ANSWER, {
            precondition: (answer: AnswerSchema) =>
              answer.identity === this.identity &&
              this.verifySignature(answer.signature),
            timeoutIn: 5000,
          }).then(({ answer }: AnswerSchema) => this.processAnswer(answer));
        }
      )
    );
  }

  send(message: ArrayBuffer) {}
}
