import Observable, { Listener } from "../../Observable";
import {
  BigNumber,
  Wallet,
  Contract,
  Transaction,
  utils,
  Signer,
  Event,
} from "ethers";
import {
  Player,
  Account,
  Credits,
  TransactionDetails,
  TransactionReceipt,
  SaleDetails,
  ContractConfig,
} from "../../types";

import constants from "../../constants";
import { timer, formatAddress, watch } from "../../utils";
import Ethereum from "../";
import RzRDB from "../../RzRDB";

// special module
import logger from "../../Log";
const log = (s: string, slot?: number) => logger.log(s, "RzR", slot);
const _log = logger._log("RzR");

export enum Transactions {
  PLAY = "play",
  MERGE_ASSETS = "mergeAssets",
  CLAIM_VICTORY = "claimVictory",
  BUY_ASSETS = "buy",
  OPEN_DISPUTE = "openDispute",
}

export enum Events {
  NEW_PLAYER = "NewPlayer",
  ASSETS_BOUGHT = "AssetsBought",
  NEW_IDENTITY = "NewIdentity",
}

export type NewPlayerEvent = {
  token: BigNumber;
  player: string;
  game: string;
  event: Event;
};

export type NewIdentityEvent = {
  address: string;
  identity: string;
  previousKey: string;
  event: Event;
};

export type AssetsBoughtEvent = {
  assetsAndToken: BigNumber;
  event: Event;
};

export type RzRContractEventMap = {
  contractLoaded: boolean;
  accountLoaded: Account;
  pendingTransactionChanged: TransactionDetails | null;
  [Transactions.MERGE_ASSETS]: TransactionReceipt;
  [Transactions.PLAY]: TransactionReceipt;
  [Transactions.CLAIM_VICTORY]: TransactionReceipt;
  [Transactions.BUY_ASSETS]: TransactionReceipt;
  [Transactions.OPEN_DISPUTE]: TransactionReceipt;

  [Events.ASSETS_BOUGHT]: AssetsBoughtEvent;
  [Events.NEW_PLAYER]: NewPlayerEvent;
  [Events.NEW_IDENTITY]: NewIdentityEvent;
};

export const digestCredits = (input: BigNumber): Credits => ({
  value: input.shr(8),
  assetRequirement: utils.arrayify(input).slice(-1)[0],
});

export default class RzRContract extends Observable<RzRContractEventMap> {
  ethereum: Ethereum;
  rzrDB: RzRDB;
  account: Account = {
    assets: [],
    credits: {
      value: BigNumber.from(0),
      assetRequirement: 0,
    },
  };
  player: Wallet | null = null;
  contract: Contract | null = null;
  pendingTransaction: null | TransactionDetails = null;
  contractDefinition: ContractConfig;

  setPendingTransaction(tx: TransactionDetails | null) {
    this.pendingTransaction = tx;
    this.emit("pendingTransactionChanged", tx);
  }

  constructor(
    contractDefinition: ContractConfig,
    ethereum: Ethereum,
    rzrDB: RzRDB
  ) {
    super();

    this.contractDefinition = contractDefinition;
    this.ethereum = ethereum;

    this.rzrDB = rzrDB;

    ethereum.addEventListener("accountChanged", this.setContract);
  }

  setContract = (signer: Signer) => {
    if (this.contract) {
      this.contract.off(Events.ASSETS_BOUGHT, this.onAssetsBought);
      this.contract.off(Events.NEW_PLAYER, this.onNewPlayer);
      this.contract.off(Events.NEW_IDENTITY, this.onNewIdentity);
    }

    this.contract = new Contract(
      this.contractDefinition.address,
      this.contractDefinition.abi,
      signer
    );

    if (signer.provider === null) return;

    this.contract.on(Events.ASSETS_BOUGHT, this.onAssetsBought);
    this.contract.on(Events.NEW_PLAYER, this.onNewPlayer);
    this.contract.on(Events.NEW_IDENTITY, this.onNewIdentity);

    this.reloadAccountInformation();

    this.emit("contractLoaded", true);
  };

  getPurchases(start?: number, end?: number): Promise<Event[]> {
    return this.getContract().queryFilter(Events.ASSETS_BOUGHT, start, end);
  }

  getNewPlayers(start?: number, end?: number): Promise<Event[]> {
    return this.getContract().queryFilter(Events.NEW_PLAYER, start, end);
  }

  getNewIdentities(start?: number, end?: number): Promise<Event[]> {
    return this.getContract().queryFilter(Events.NEW_IDENTITY, start, end);
  }

  getGame(token: BigNumber) {
    return this.getContract().games(token);
  }

  expired(token: BigNumber) {
    return this.getContract().expiredEntities(token);
  }

  getPlayer(address: string): Promise<string> {
    return this.getContract().identities(address);
  }

  getAssets(address: string | Uint8Array) {
    return this.getContract()
      .userAssets(utils.hexlify(address))
      .then((assets: BigNumber) => utils.arrayify(assets).reverse());
  }

  onAssetsBought = (assetsAndToken: BigNumber, event: Event) => {
    this.emit(Events.ASSETS_BOUGHT, { assetsAndToken, event });
  };

  onNewPlayer = (
    token: BigNumber,
    player: string,
    game: string,
    event: Event
  ) => {
    this.emit(Events.NEW_PLAYER, {
      token,
      player: player.toLowerCase(),
      game: game.toLowerCase(),
      event,
    });
  };

  onNewIdentity = (player: string, identity: string, previousKey: string, event: Event) => {
    this.emit(Events.NEW_IDENTITY, {
      address: player.toLowerCase(),
      identity: identity.toLowerCase(),
      previousKey,
      event
    });
  };

  transact(
    method: Transactions,
    params: any[],
    transactionDetails: TransactionDetails
  ) {
    log(`initiating transaction`);
    let slot = log(`checking requirements...`);
    let txSlot = -1;

    log(`done`, slot);
    slot = log(`waiting for approval...`);
    this.setPendingTransaction(transactionDetails);

    return this.getContract()
      [method](...params)
      .then((tx: Transaction) => {
        log("done", slot);
        this.setPendingTransaction(null);

        txSlot = log("waiting for transaction completion...");
        return watch<TransactionReceipt>(async () => {
          const receipt = await this.ethereum.provider.getTransactionReceipt(
            tx.hash as string
          );

          if (receipt?.blockNumber) return receipt;

          return null;
        }, 1000);
      })
      .then((tx: TransactionReceipt) => {
        log(`done (block number: ${tx.blockNumber})`, txSlot);
        this.emit(method, tx);
        this.reloadAccountInformation();
        return tx;
      })
      .catch((e: any) => {
        this.setPendingTransaction(null);
        log("failed", slot);
        throw e;
      });
  }

  getContract() {
    if (!this.contract) {
      this.setPendingTransaction(null);
      throw new Error("contract not ready");
    }
    return this.contract;
  }

  async reloadAccountInformation() {
    const contract = this.getContract();
    const [credits, assets, _identity] = await _log(
      "requesting account information",
      Promise.all([
        contract.userCredits(this.ethereum.address),
        this.getAssets(this.ethereum.address),
        contract.identities(this.ethereum.address),
      ])
    );

    const identity = _identity.toLowerCase();

    // TODO: what happens if I miss the new player event?
    // way too much reliance on events, or is it?
    // events are basically the backbone of everything
    // what if I don't log in for 4 weeks?
    // how does compression work then?
    // in any case, for now, we should just accept Ethereum's events
    // and the current state
    // but we somehow need to get to the point where it has changed
    // wait events are indexed!

    if (identity !== this.ethereum.identity?.address.toLowerCase()) {
      // throw new Error('identity failure')

      /*
      this.rzrDB.storePlayer({
        this.ethereum.address
      })*/
    }

    this.account = {
      assets,
      credits: digestCredits(credits),
    };

    this.emit("accountLoaded", this.account);
    /*
    log(`assets: ${utils.arrayify(assets)}`);
    log(`credits: ${utils.formatEther(credits.value)}`);
    */
  }

  async buy({
    expiry,
    r: _r,
    timestamp,
    assets,
    signature,
    price,
    address,
  }: SaleDetails) {
    const assetsAndToken = utils.concat([
      utils.zeroPad(utils.arrayify(expiry), 2),
      utils.zeroPad(utils.arrayify(timestamp), 8),
      utils.zeroPad(utils.arrayify(_r), 2),
      utils.zeroPad(utils.arrayify(assets), 20),
    ]);
    const { v, r, s } = utils.splitSignature(signature);

    return this.transact(
      Transactions.BUY_ASSETS,
      [
        utils.hexlify(assetsAndToken),
        address,
        v,
        r,
        s,
        {
          value: price.toString(),
        },
      ],
      {
        name: "Buy assets",
        params: [
          {
            name: "Asset",
            value: BigNumber.from(assets).toHexString(),
          },
          {
            name: "Seller",
            value: formatAddress(address),
          },
          {
            name: "Price",
            value: utils.formatEther(price),
          },
        ],
      }
    );
  }

  mergeAssets(assetsFrom: BigNumber, assetTo: BigNumber) {
    return this.transact(Transactions.MERGE_ASSETS, [assetsFrom, assetTo], {
      name: "Merge assets",
      params: [
        {
          name: "assetsFrom",
          value: utils.hexlify(BigNumber.from(assetsFrom)),
        },
        {
          name: "assetsTo",
          value: utils.hexlify(BigNumber.from(assetTo)),
        },
      ],
    });
  }

  play(gameAddress: string, value: BigNumber, useNewIdentity = false) {
    const token =
      value.mod(1e14) > BigNumber.from(0)
        ? value
        : value.add(BigNumber.from(utils.randomBytes(4)));

    return (
      useNewIdentity || this.ethereum.identity === null
        ? this.rzrDB.storeNextIdentityAddress(this.ethereum.address)
        : Promise.resolve(this.ethereum.identity)
    ).then((player) =>
      this.transact(
        Transactions.PLAY,
        [
          gameAddress,
          player.address,
          utils.keccak256(player.privateKey),
          {
            value: token,
          },
        ],
        {
          name: "Play",
          params: [
            {
              name: "player",
              value: formatAddress(player.address, 5),
            },
            { name: "game", value: formatAddress(gameAddress, 5) },
            {
              name: "wager",
              value: utils.formatEther(value.sub(value.mod(1e14))),
            },
          ],
        }
      )
    );
  }

  claimVictory(_token: BigNumber, signature: Uint8Array) {
    const { v, r, s } = utils.splitSignature(signature);
    const token = utils.hexlify(utils.zeroPad(utils.arrayify(_token), 32));
    let pendingSlot: number;
    return this.transact(Transactions.CLAIM_VICTORY, [token, v, r, s], {
      name: "Claim victory",
      params: [
        {
          name: "Token",
          value: utils.hexlify(BigNumber.from(token)),
        },
      ],
    })
      .then(() => {
        pendingSlot = log("verifying claim...");
        return timer(5000); // give time for blockchain to actually reflect the change
      })
      .then(() => this.getContract().getPlayerByIndex(token, 0))
      .then((result: string) => {
        if (
          result !==
          utils.hexlify(utils.zeroPad("0x", constants.ADDRESS_LENGTH))
        ) {
          log("failed", pendingSlot);
          return;
        }
        log("done", pendingSlot);

        return this.rzrDB.removeGameAndSignatures(utils.hexlify(_token));
      });
  }
}
