import { BigNumber, utils } from "ethers";
import { Shard, SHARDS, SaleDetails } from "./rzr";
export const numberToTimeString = (n: number): string => {
  if (n < 0) return "0m 0s";
  const milliseconds = n % 1000;
  n -= milliseconds;
  n /= 1000;
  const seconds = n % 60;
  n -= seconds;
  if (n === 0) {
    return `0m ${seconds}s`;
  }
  n /= 60;
  const minutes = n % 60;
  n -= minutes;
  if (n === 0) {
    return `${minutes}m ${seconds}s`;
  }
  n /= 60;
  const hours = n % 60;
  n -= hours;
  if (n === 0) {
    return `${hours}h ${minutes}m`;
  }
  n /= 60;
  const days = n % 24;
  return `${days}d ${hours}h ${minutes}m`;
};

export const formatAddress = (address: string, n: number = 3): string =>
  `${address.slice(0, 2 + n)}...${address.slice(-n)}`;

export const formatHex = (address: string, n: number = 3): string =>
  `${address.slice(0, 2 + n)}...${address.slice(-n)}`;

export const timer = (timeout: number):Promise<undefined> =>
  new Promise((r) => setTimeout(r, timeout));

export const watch = async <T>(
  watcher: () => T | null | Promise<T | null>,
  t = 250,
  maxRetries = 1000,
): Promise<T> => {
  const value = await watcher();

  if (value !== null) {
    return value;
  }

  if (maxRetries === 0)  throw new Error('failed')

  await timer(t * 1.1);

  return watch(watcher, t, maxRetries - 1);
};

export const bytesToNumber = (i: Uint8Array): number =>
  parseInt(utils.hexlify(i));

export const getShardIndex = (shard: Shard) =>
  SHARDS.findIndex((s) => s.sign === shard.sign && s.value === shard.value);

export const getShardAsAsset = (shard: Shard) =>
  BigNumber.from("1").shl(getShardIndex(shard) * 8);

export const digestAssetsAndToken = (data: Uint8Array) => ({
  expiry: bytesToNumber(data.slice(0, 2)),
  timestamp: bytesToNumber(data.slice(2, 10)),
  r: bytesToNumber(data.slice(10, 12)),
  assets: BigNumber.from(data.slice(12, 32)),
});

export const digestSale = (sale: Uint8Array) => ({
  ...digestAssetsAndToken(sale),
  price: BigNumber.from(sale.slice(32, 64)),
  signature: sale.slice(64, 129),
  address: utils.hexlify(sale.slice(129, 149)),
});

export const compressSale = (sale: SaleDetails) => {
  const assetsAndToken = utils.concat([
    utils.zeroPad(utils.arrayify(sale.expiry), 2),
    utils.zeroPad(utils.arrayify(sale.timestamp), 8),
    utils.zeroPad(utils.arrayify(sale.r), 2),
    utils.zeroPad(utils.arrayify(sale.assets), 20),
  ]);

  const priceBytes = utils.zeroPad(utils.arrayify(sale.price), 32);

  return utils.concat([
        assetsAndToken,
        priceBytes,
        utils.arrayify(sale.signature),
        utils.arrayify(sale.address),
      ])
};
