import RzRGame, { Games, Opponent } from "./RzRGame";
import { PeerMessageType, CustomMessageSchema } from "../../RzRPeer";
import { Contract, BigNumber, Signer, utils } from "ethers";
import ethereum from "../";
import { timer, watch } from "../../utils";
import logger from "../../Log";
import { SHARDS, ShardElement } from "../../types";

const log = (s: string, slot?: number) => logger.log(s, "RzRShards", slot);

// to not overload the contract
const xorshift64 = (n: Uint8Array): Uint8Array => {
  const to64bits = (_n: BigNumber): BigNumber =>
    BigNumber.from(utils.arrayify(_n).slice(-8));

  let bn = to64bits(BigNumber.from(n));

  bn = bn.xor(bn.shr(12));
  bn = bn.xor(to64bits(bn.shl(25)));
  bn = bn.xor(bn.shr(27));

  return utils.arrayify(bn);
};

const countSetBits = (n: number) => {
  let count = 0;
  while (n > 0) {
    n &= n - 1;
    ++count;
  }

  return count;
};

const expandByte = (n: number): BigNumber => {
  return BigNumber.from(`0x${n.toString(2).replaceAll("1", "f")}`);
};

// declaredAssets: ethers.utils.arrayify("0x12222111"), // (+4 +3 +3 +4)

const parseSequence = (s: number[]): number[] =>
  s.reduce((s: number[], e) => [e & 0x0f, e >> 4, ...s], []);

const hasRevealed = (statusByte: number, playerIndex: number): boolean =>
  (statusByte & (1 << (3 + playerIndex))) > 0;
// this should just take care of when and how the actions are sent and digest the game data, applying the actions are up to the SC that's why it's there, the whole point is that you are technicall supposed to "run your own node with verifiability"
// initial priority management
export type RzRShardState = {
  priority: number;
  boardSizes: [number, number];
  gameProgress: number;
  secretsDeclared: [boolean, boolean];
  isStanding: [boolean, boolean];
  hasRevealed: [boolean, boolean];
  assetsChosen: [boolean, boolean];
  boards: [ShardElement[], ShardElement[]];
  myIndex: number;
  sequencePosition: number;
  sequences: [number[], number[]];
  setResults: [number, number];
  isLastElementReplacable: boolean;
  availableAssets: Array<number | null>;
  opponentUsedAssetCount: number;
};

const selectedAssets = "0x11111111";

export default class RzRShards extends RzRGame<RzRShardState> {
  name: Games = "RzRShards";
  seed = utils.randomBytes(8);
  declaredAssets: number[] = []; // you actually need to compress things here
  turn: number[] = [];
  turnState: Uint8Array | null = null;
  idleTimeout: number = 0;
  usedAssetsMask: number = 0; // 1 byte 0b10101010

  // use applyAction from contract...

  revealProof() {
    return utils.concat([this.seed, selectedAssets]);
  }
  //0x26f65a1869b6b7f26a063e910aa132ff796f

  /*
         state breakdown (v2)
     220 2 bytes: set results
     216 4 bytes: used assets
     184 32 bytes: hash player 1
     183 1 byte: player 1 mask
     151 32 bytes: hash player 2 
     150 1 byte: player 2 mask
     86  64 bytes sequence player 2
     22  64 bytes sequence player 1
     6   16 bytes: current board
     5   1 byte: next asset index to be revealed for each player // may not be needed
     4   1 byte sequence position = 7 bits for position last bit for parity
     2   2 bytes next reveal item position on board
     1   1 byte state (first priority, standing status, seq revealed status)
     0   1 byte: action count
     */
  parseState(rawGameData: Uint8Array): RzRShardState {
    const actionCount = rawGameData[0];
    const board = [...new Array(16)].map((_, i) => rawGameData[6 + i]);

    const assetsChosen = [rawGameData[150] !== 0, rawGameData[183] !== 0] as [
      boolean,
      boolean
    ];

    const secretsDeclared = [
      BigNumber.from(utils.hexlify(rawGameData.slice(184, 216))).toString() !==
        "0",

      BigNumber.from(utils.hexlify(rawGameData.slice(151, 183))).toString() !==
        "0",
    ] as [boolean, boolean];

    const boardSizes = [rawGameData[2], rawGameData[3]] as [number, number];
    const sequencePosition = rawGameData[4];
    const hasRevealed = [
      (rawGameData[1] & 0b01000) > 0,
      (rawGameData[1] & 0b10000) > 0,
    ] as [boolean, boolean];

    const setResults = [
      rawGameData[220] & 0b11,
      (rawGameData[220] & 0b1100) >> 2,
      (rawGameData[220] & 0b110000) >> 4,
      (rawGameData[220] & 0b11000000) >> 6,
      rawGameData[221] & 0b11,
      (rawGameData[221] & 0b1100) >> 2,
      (rawGameData[221] & 0b110000) >> 4,
      (rawGameData[221] & 0b11000000) >> 6,
    ];

    console.log(setResults);

    const boards = [board.slice(0, 8), board.slice(8, 16)] as [
      number[],
      number[]
    ];

    const usedAssetsMasks = [
      (255 ^ this.usedAssetsMask) & 0xf,
      (255 ^ this.usedAssetsMask) >> 4,
    ];

    const declaredAssets = this.rawGameData[this.myIndex === 0 ? 183 : 150];

    const availableAssets: Array<number | null> = declaredAssets
      ? Array.from(
          utils.zeroPad(
            new Uint8Array(
              Array.from(
                utils
                  .hexlify(
                    BigNumber.from(
                      `0x${utils
                        .hexlify(
                          BigNumber.from(selectedAssets).and(
                            expandByte(
                              this.rawGameData[this.myIndex === 0 ? 183 : 150]
                            )
                          )
                        )
                        .slice(2)
                        .replaceAll("0", "")}`
                    ).and(expandByte(usedAssetsMasks[this.myIndex]))
                  )
                  .slice(2)
              ).map((i) => parseInt(i, 16))
            ),
            4
          )
        ).map((i) => (i === 0 ? null : i - 1))
      : [null, null, null, null];

    // ^ I suppose this could be simplified...

    return {
      availableAssets,
      opponentUsedAssetCount:
        4 - countSetBits(usedAssetsMasks[this.myIndex ^ 1]),
      hasRevealed,
      boardSizes,
      secretsDeclared,
      assetsChosen,
      sequencePosition,
      boards: [
        boards[0].map((v) =>
          v > 15 ? SHARDS[v & 0x0f] : { value: 8 - (v & 7) }
        ),
        boards[1].map((v) =>
          v > 15 ? SHARDS[v & 0x0f] : { value: 8 - (v & 7) }
        ),
      ],
      gameProgress: actionCount,
      priority: (actionCount & 0x1) ^ (rawGameData[1] & 0x1),
      myIndex: this.myIndex,
      isStanding: [
        (rawGameData[1] & 0x02) === 0x02,
        (rawGameData[1] & 0x04) === 0x04,
      ],
      sequences: [
        parseSequence(Array.from(rawGameData.slice(22, 86))).slice(
          0,
          sequencePosition + (hasRevealed[0] ? 1 : 0)
        ),
        parseSequence(Array.from(rawGameData.slice(86, 150))).slice(
          0,
          sequencePosition + (hasRevealed[1] ? 1 : 0)
        ),
      ],
      setResults: setResults.reduce(
        (sums, i) =>
          i === 1
            ? [sums[0] + 1, sums[1]]
            : i === 2
            ? [sums[0], sums[1] + 1]
            : sums,
        [0, 0]
      ) as [number, number],
      isLastElementReplacable:
        boards[this.myIndex][boardSizes[this.myIndex] - 1] > 7,
    };
  }

  iterate(skip = 0) {
    const slot = log("calculating next iteration...");

    const { sequencePosition } = this.parseState(this.rawGameData);

    const position =
      sequencePosition +
      (hasRevealed(this.rawGameData[1], this.myIndex) ? 1 : 0);

    let iteration = {
      x: this.seed,
      count: 0,
    };

    do {
      iteration = {
        x: xorshift64(iteration.x),
        count: iteration.count + 1,
      };
      // console.log(iteration.x[0] >> 4);
    } while (iteration.count <= position + skip);

    const n = iteration.x[0] >> 4;

    log("done", slot);

    return n;
  }

  playAsset = (assetIndex: number, replace = false) => {
    const asset = this.parseState(this.rawGameData).availableAssets[assetIndex];

    if (asset === null) return;

    this.usedAssetsMask += 1 << (3 - assetIndex + (this.myIndex === 0 ? 0 : 4));

    const element = asset | (replace ? 0x20 : 0x10);

    // apply variations
    this.turn.push(element);

    this.revealNext(this.myIndex, element, replace);

    this.opponent.peer.send(PeerMessageType.GAME_CUSTOM_MESSAGE_2, {
      data: new Uint8Array([assetIndex, element, replace ? 1 : 0]),
    });
  };

  private generateSecret() {
    return utils.keccak256(
      utils.concat([this.seed, utils.arrayify(selectedAssets)])
    );
  }

  async processSideEffects(
    gameData: Uint8Array,
    action: Uint8Array,
    playerIndex: number
  ) {
    return timer(1000);
  }

  // must process actions as they come, one by one
  //
  // what the hell is 15?
  // ooh it's 0b00001111
  // it's the selected stuff

  handleFirstRevealAsOddPlayer(firstRevealedParity: number) {
    const n1 = this.iterate();

    if (((n1 & 0x01) ^ firstRevealedParity) === 1) {
      this.sendAction(new Uint8Array([15, n1, this.iterate(1)]));
      return;
    }
    return this.sendAction(new Uint8Array([15, n1]));
  }

  async respondToGameDataChange() {
    const { gameProgress, sequences } = this.parseState(this.rawGameData);
    switch (gameProgress) {
      case 1:
        return this.sendInitialMessage();
      case 2:
        return this.sendAction(new Uint8Array([15, this.iterate()]));
      case 3:
        return this.handleFirstRevealAsOddPlayer(sequences[0][0] & 0x01);
      // if prio is player 1 return 2 numbers
      default:
        this.startTurn();
    }
  }

  startTurn() {
    // this.idleTimeout = han
    if (this.turnState && this.turn.length > 0) {
      this.emit("gameChanged", this.parseState(this.turnState));
      return;
    }
    this.turnState = this.rawGameData;
    const { isStanding, sequencePosition, sequences } = this.parseState(
      this.turnState
    );

    if (isStanding[this.myIndex]) {
      this.turn = [this.iterate()];
      return this.endTurn();
    }

    // if opponent has revealed we need to reveal another one..
    if (hasRevealed(this.turnState[1], this.myIndex ^ 1)) {
      this.turn = [this.iterate(), 0, this.iterate(1)];
      // set board position
      const value =
        sequences[this.myIndex ^ 1][sequencePosition] ^ this.turn[0];

      this.revealNext(this.myIndex, value);

      // also let the opponent know about the *first reveal
      this.opponent.peer.send(PeerMessageType.GAME_CUSTOM_MESSAGE_1, {
        data: new Uint8Array([value]),
      });
    } else {
      this.turn = [this.iterate()];
    }
  }

  endTurn = (isStanding?: boolean) => {
    if (isStanding) {
      this.turn[1] = 1;
    }

    clearTimeout(this.idleTimeout);

    this.sendAction(new Uint8Array(this.turn));

    this.turn = [];

    this.turnState = null;
  };

  sendInitialMessage = () =>
    this.sendAction(utils.arrayify(this.generateSecret()));

  revealNext(playerIndex: number, value: number, replace = false) {
    this.turnState = new Uint8Array(this.turnState || this.rawGameData);
    const { boardSizes } = this.parseState(this.turnState);

    // set board
    this.turnState[
      6 + playerIndex * 8 + boardSizes[playerIndex] - (replace ? 1 : 0)
    ] = value;

    // increase board size
    if (!replace) this.turnState[2 + playerIndex] += 1;

    this.emit("gameChanged", this.parseState(this.turnState));
  }

  handleReveal = ({ data }: CustomMessageSchema) => {
    this.revealNext(this.myIndex ^ 1, data[0]);
  };

  handleAssetPlay = ({ data }: CustomMessageSchema) => {
    if (data[0] > 3) return;

    this.usedAssetsMask += 1 << (data[0] + (this.myIndex === 0 ? 4 : 0));

    this.revealNext(this.myIndex ^ 1, data[1], data[2] === 1);
  };

  attachListeners() {
    super.attachListeners();

    this.opponent.peer.addEventListener(
      PeerMessageType.GAME_CUSTOM_MESSAGE_1,
      this.handleReveal
    );

    this.opponent.peer.addEventListener(
      PeerMessageType.GAME_CUSTOM_MESSAGE_2,
      this.handleAssetPlay
    );
  }

  removeListeners() {
    super.removeListeners();

    this.opponent.peer.removeEventListener(
      PeerMessageType.GAME_CUSTOM_MESSAGE_1,
      this.handleReveal
    );

    this.opponent.peer.removeEventListener(
      PeerMessageType.GAME_CUSTOM_MESSAGE_2,
      this.handleAssetPlay
    );
  }

  startGame() {
    if (this.myIndex === 0) {
      this.sendInitialMessage();
    }
  }

  getGameProgress(rawGameData?: Uint8Array) {
    return (rawGameData || this.rawGameData)[0];
  }

  private validateUsedAssets() {
    const usedAssets = [
      [
        this.rawGameData[216] & 0x0f,
        this.rawGameData[217] & 0x0f,
        this.rawGameData[218] & 0x0f,
        this.rawGameData[219] & 0x0f,
      ].filter((i) => i !== 0xf),
      [
        this.rawGameData[216] >> 4,
        this.rawGameData[217] >> 4,
        this.rawGameData[218] >> 4,
        this.rawGameData[219] >> 4,
      ].filter((i) => i !== 0xf),
    ];

    // ^^ of course this won't actually know the index of the item used,
    // that could be stored separately if the information is available
    const availableAssetsHex = utils
      .hexlify(
        BigNumber.from(selectedAssets).and(
          expandByte(this.rawGameData[this.myIndex === 0 ? 183 : 150])
        )
      )
      .slice(2)
      .replaceAll("0", "");

    // need to get picked assets
    const availableAssets: Array<number | null> = Array.from(
      availableAssetsHex
    ).map((i) => parseInt(i, 16) - 1);

    const usedAssetsMasks = [
      (255 ^ this.usedAssetsMask) & 0xf,
      (255 ^ this.usedAssetsMask) >> 4,
      //0b0111,
    ];

    const availableAssets2 = Array.from(
      utils
        .hexlify(
          BigNumber.from(`0x${availableAssetsHex}`).and(
            expandByte(usedAssetsMasks[this.myIndex])
          )
        )
        .slice(2)
    ).map((i) => (i === "0" ? null : parseInt(i, 16) - 1));

    let availableValue = 0;
    let availableValue2 = 0;
    let alternativeMask = 0;
    for (let i = 0; i < 4; ++i) {
      const index = usedAssets[this.myIndex].findIndex(
        (n) => n === availableAssets[i]
      );

      if (availableAssets2[i] !== null) {
        availableValue2 += 1 << (availableAssets[i] as number);
      }

      if (index >= 0) {
        delete usedAssets[this.myIndex][index];
        availableAssets[i] = null;
        alternativeMask += 1 << i;
      } else {
        availableValue += 1 << (availableAssets[i] as number);
      }
    }

    if (availableValue < availableValue2) {
      this.usedAssetsMask =
        this.myIndex === 0
          ? (this.usedAssetsMask & 0xf0) | alternativeMask
          : (this.usedAssetsMask & 0x0f) | (alternativeMask << 4);
    }
  }

  replaceGameData(rawGameData: Uint8Array) {
    super.replaceGameData(rawGameData);

    this.validateUsedAssets();
  }

  parseGameData(gameData: Uint8Array): Uint8Array {
    // load used asset indices
    // could be just one byte
    // 1001 -> player 0  (1 = available, 0 = used)
    // 1111 -> player 1
    // if we want to be really fancy, we can even just mask it
    // do check what the hell happens
    // make sure that's in line with on actual game data though..

    this.usedAssetsMask = gameData[0];

    this.seed = gameData.slice(1, 9);

    this.replaceGameData(gameData.slice(9));

    return this.rawGameData;
  }

  serialize() {
    return utils.concat([
      new Uint8Array([this.usedAssetsMask]),
      this.seed,
      this.rawGameData,
    ]);
  }
}
