import { Signer, Contract, utils, Wallet } from "ethers";
import RzRPeer, {
  PeerMessageType,
  ActionSchema,
  ActionResponseSchema,
  GameOverSchema,
  GameReadyWithDataSchema,
  RevealSecretSchema,
} from "../../RzRPeer";
import Observable, { Listener } from "../../Observable";
import { watch, timer, formatHex, once } from "../../utils";
import Ethereum from "../";
import RzRDB from "../../RzRDB";
import RzRContract from "./RzR";
import logger from "../../Log";
import { ContractConfig } from "../../types";

const log = (s: string, slot?: number) => logger.log(s, "RzRGame", slot);
const _log = logger._log("RzRGame");

export type Opponent = {
  isConnected: boolean;
  address: string;
  identity: string;
  peer: RzRPeer;
  isPlaying: boolean;
};

type RzRGameEventMap = {
  opponentConnectionStatusChanged: boolean;
  loadingStatusChanged: boolean;
  gameOver: boolean;
  contractLoaded: boolean;
};

export type Games = "RzRShards" | "RzR";
type LockType = "gameData" | "socket";

// game state message breakdown
// (progress embedded in state)
// 65 bytes signature | game state
// ack message -> progress

// game
//
// I send an action, I receive a signature
// game message therefore: action -> signature or denial
// priority should be managed by clients
const prepareGameDataForSignature = (
  token: string,
  gameData: Uint8Array | string
) => utils.keccak256(utils.concat([utils.zeroPad(token, 32), gameData]));

export default abstract class RzRGame<
  GameState extends { priority: number }
> extends Observable<RzRGameEventMap & { gameChanged: GameState }> {
  modules: {
    ethereum: Ethereum;
    rzrDB: RzRDB;
    rzrContract: RzRContract;
  };

  latestSent: {
    acknowledged: boolean;
    actionId: number;
  } | null = null;

  latestRecv: number = 0;

  // each state change must be ACKd
  // can't be sure that the peer is listening

  contract: Contract | null = null;
  isLoading = true;
  locks: { [key in LockType]: boolean } = { socket: false, gameData: false };
  myIndex: number = -1;
  name: Games = "RzR";
  opponent: Opponent;
  hasStarted = false;
  hasLoaded = false;
  token: string;
  inDispute: boolean = false;
  pendingPlayer: number = 2;
  rawGameData: Uint8Array = new Uint8Array();
  initialStateData: Uint8Array = new Uint8Array();
  contractDefinition: ContractConfig;

  constructor(
    contractDefintion: ContractConfig,
    token: string,
    peer: RzRPeer,
    rzrContract: RzRContract,
    ethereum: Ethereum,
    rzrDB: RzRDB
  ) {
    super();

    this.contractDefinition = contractDefintion;

    this.token = token;
    this.modules = {
      rzrDB,
      rzrContract,
      ethereum,
    };
    this.opponent = {
      peer: peer,
      address: "",
      identity: "",
      isConnected: false,
      isPlaying: false,
    };

    this.build();
  }

  protected startGame() {}
  protected revealProof(): Uint8Array {
    return new Uint8Array();
  }
  protected getGameProgress(rawGameData?: Uint8Array): number {
    return 0;
  }
  protected parseState(rawGameData: Uint8Array): GameState {
    return {} as any;
  }
  protected processSideEffects(
    gameData: Uint8Array,
    action: Uint8Array,
    playerIndex: number
  ) {}
  protected respondToGameDataChange() {}

  // could use schemas
  protected serialize(): Uint8Array {
    return this.rawGameData;
  }

  protected parseGameData(gameData: Uint8Array): Uint8Array {
    return this.rawGameData;
  }

  protected replaceGameData(gameData: Uint8Array) {
    this.rawGameData = gameData;
  }

  protected setContract = (signer: Signer) => {
    this.contract = new Contract(
      this.contractDefinition.address,
      this.contractDefinition.abi,
      signer
    );

    // TODO: need to add event listeners for disputes
  };

  protected attachListeners() {
    this.modules.ethereum.addEventListener("accountChanged", this.setContract);

    this.opponent.peer.addEventListener(
      "connectionStatusChanged",
      this.handleOpponentConnectionChange
    );

    this.opponent.peer.addEventListener(
      PeerMessageType.GAME_OVER,
      this.onEndGame
    );

    this.opponent.peer.addEventListener(
      PeerMessageType.REVEAL_SECRET,
      this.onSecret
    );

    this.opponent.peer.addEventListener(
      PeerMessageType.GAME_ACTION,
      this.processAction
    );
  }

  protected sendAction(_action: Uint8Array) {
    const actionId = (this.latestSent?.actionId || 0) + 1;
    const action = utils.concat([
      new Uint8Array(_action),
      new Uint8Array(32 - _action.length),
    ]);

    return _log(
      `sending action ${formatHex(utils.hexlify(action), 6)}`,
      this.signAction(action).then((signature) => {
        if (!signature) throw new Error("invalid action 1");

        this.latestSent = {
          actionId,
          acknowledged: false,
        };

        this.opponent.peer.send(PeerMessageType.GAME_ACTION, {
          actionId,
          signature,
          actionData: action,
        });

        return once(this.opponent.peer, PeerMessageType.GAME_ACTION_RESPONSE, {
          precondition: (response) => response.actionId === actionId,
        }).then((response) => {
          // defends against reentry
          if (!response.response || actionId < (this.latestRecv || 0)) {
            debugger;
            throw new Error("invalid action 2");
          }

          this.latestRecv = actionId;
        });
      })
    );
  }

  private build() {
    _log(
      "initializing game",
      this.modules.rzrDB
        .loadPlayer(this.modules.ethereum.address)
        .then((player) => {
          if (
            !player ||
            this.modules.ethereum.identity?.address.toLowerCase() !== player.identity
          )
            throw new Error("player not found");
          return this.modules.rzrDB.loadGame(this.token);
        })
        .then((game) => {
          if (!game) throw new Error("identity or game not found");

          if (this.modules.ethereum.account)
            this.setContract(this.modules.ethereum.account);

          this.myIndex = game.players.findIndex(
            (addr) => this.modules.ethereum.address.toLowerCase() === addr
          );
          this.opponent.address = game.players[this.myIndex ^ 1].toLowerCase();
          return this.modules.rzrContract.getPlayer(this.opponent.address);
        })
        .then((identity) => {
          this.opponent.identity = identity.toLowerCase();

          return watch(() => this.contract);
        })
        .then((contract) => contract.getInitialState())
        .then((initialState) => {
          this.initialStateData = utils.arrayify(initialState);

          this.attachListeners();

          this.isLoading = false;

          this.emit("loadingStatusChanged", this.isLoading);

          this.handleOpponentConnectionChange(this.opponent.peer.isConnected);
        })
    );
  }

  private handleOpponentConnectionChange = (isConnected: boolean) => {
    this.emit("opponentConnectionStatusChanged", false);

    if (isConnected) this.introduceOpponent();
    else {
      this.hasLoaded = false;
      this.opponent.isPlaying = false;
    }
  };

  private async introduceOpponent() {
    const game = await this.modules.rzrDB.loadGameData(this.token);
    let payload: GameReadyWithDataSchema;
    if (!game) {
      this.rawGameData = this.initialStateData;
      this.opponent.peer.send(PeerMessageType.GAME_READY);
    } else {
      this.rawGameData = this.parseGameData(game.gameData);

      this.hasStarted = true;

      payload = {
        signature: game.signature,
        gameData: this.rawGameData,
      };

      this.opponent.peer.send(PeerMessageType.GAME_DATA, { ...payload });
    }

    // they could technically respond with a message
    // that's a hash of the last game state
    // and if it matches
    // no need to send second introduction
    return Promise.race([
      once(this.opponent.peer, PeerMessageType.GAME_DATA, {
        timeoutIn: 60000,
        silent: true,
      }),
      once(this.opponent.peer, PeerMessageType.GAME_READY, {
        timeoutIn: 60000,
        silent: true,
      }),
    ])
      .then((response) => {
        if (response.gameData) {
          const { gameData, signature } = response as GameReadyWithDataSchema;
          const slot = log("processing incoming game state...");

          if (
            this.isValidSignature(signature, gameData, true) &&
            this.getGameProgress(gameData) >= this.getGameProgress()
          ) {
            this.hasStarted = true;

            this.replaceGameData(gameData);

            log("success", slot);
          } else {
            log("failed", slot);

            this.opponent.isPlaying = true;

            console.log(payload);
            this.opponent.peer.send(PeerMessageType.GAME_DATA, payload);
          }
        }

        if (!this.opponent.isPlaying) {
          this.opponent.isPlaying = true;

          this.opponent.peer.send(PeerMessageType.GAME_READY);
        }

        this.emit("opponentConnectionStatusChanged", true);

        this.emit("gameChanged", this.parseState(this.rawGameData));

        return timer(1000);
      })
      .then(this.checkGameOverConditions)
      .then((isGameOver) => {
        if (isGameOver) return;
        if (!this.hasStarted) {
          this.hasStarted = true;
          this.startGame();
        } else if (
          !this.hasLoaded &&
          this.parseState(this.rawGameData).priority === this.myIndex
        ) {
          this.hasLoaded = true;
          this.respondToGameDataChange();
        }
      });
  }

  private isValidSignature(
    signature: string | Uint8Array,
    gameData: string | Uint8Array,
    isSelf = false
  ): boolean {
    return (
      (isSelf ? this.player.address.toLowerCase() : this.opponent.identity) ===
      utils
        .verifyMessage(
          prepareGameDataForSignature(this.token, gameData),
          signature
        )
        .toLowerCase()
    );
  }

  private applyAction = async (
    action: Uint8Array,
    signature?: Uint8Array
  ): Promise<boolean> => {
    if (!this.contract) return false;

    let slot = 0;
    if (signature) {
      slot = log(`processing action ${formatHex(utils.hexlify(action), 6)}...`);
    }
    const initialGameData = utils.hexlify(this.rawGameData);

    const newGameData = await this.contract.applyAction(
      initialGameData,
      utils.hexlify(action),
      signature ? this.myIndex ^ 1 : this.myIndex
    );

    if (
      newGameData === initialGameData ||
      (signature && !this.isValidSignature(signature, newGameData))
    ) {
      log("failed", slot);
      return false;
    }

    this.rawGameData = utils.arrayify(newGameData);

    if (signature) {
      this.modules.rzrDB.storeGameData(this.token, {
        signature,
        gameData: this.serialize(),
      });

      log("done", slot);
    }

    await this.processSideEffects(
      utils.arrayify(initialGameData),
      action,
      signature ? this.myIndex ^ 1 : this.myIndex
    );

    this.emit("gameChanged", this.parseState(this.rawGameData));

    if (signature) this.respondToGameDataChange();

    this.checkGameOverConditions();

    return true;
  };

  private checkGameOverConditions = async () => {
    const slot = log("checking game over conditions...");
    if (!this.contract) return;
    if (
      (await this.contract.getGameWinner(this.rawGameData)) === this.myIndex
    ) {
      this.opponent.peer.send(PeerMessageType.REVEAL_SECRET, {
        proof: this.revealProof(),
      });

      log("conditions met!", slot);

      return true;
    }

    log("done", slot);
    return false;
  };

  private processAction = ({ actionId, actionData, signature }: ActionSchema) =>
    this.applyAction(actionData, signature).then((actionResult) =>
      this.opponent.peer.send(PeerMessageType.GAME_ACTION_RESPONSE, {
        actionId,
        response: actionResult,
      })
    );

  private onSecret = async ({ proof }: RevealSecretSchema) => {
    if (!this.contract) return;
    const slot = log("validating results...");
    if (
      await this.contract.isValidProof(
        new Uint8Array([this.myIndex ^ 1]),
        this.rawGameData,
        proof
      )
    ) {
      log("done", slot);
      return this.endGame();
    }
    log("failed", slot);
  };

  private onEndGame = async ({ signature }: GameOverSchema) => {
    if (!this.contract) return;
    this.modules.rzrDB.storeSignature(this.token, signature);
    this.emit("gameOver", true);
  };

  protected removeListeners() {
    this.opponent.peer.removeEventListener(
      "connectionStatusChanged",
      this.handleOpponentConnectionChange
    );
    this.opponent.peer.removeEventListener(
      PeerMessageType.GAME_OVER,
      this.onEndGame
    );
    this.opponent.peer.removeEventListener(
      PeerMessageType.REVEAL_SECRET,
      this.onSecret
    );
    this.opponent.peer.removeEventListener(
      PeerMessageType.GAME_ACTION,
      this.processAction
    );
  }

  // TODO: this is outdated, since identities are captured on game level we can now enable multi game identity games
  private get player() {
    if (!this.modules.ethereum.identity)
      throw new Error("identity not available");

    return this.modules.ethereum.identity;
  }

  private signAction = async (
    action: Uint8Array
  ): Promise<Uint8Array | null> => {
    if (!(await this.applyAction(action))) return null;

    return this.player
      .signMessage(prepareGameDataForSignature(this.token, this.rawGameData))
      .then(utils.arrayify);
  };

  endGame = () => {
    return this.player
      .signMessage(
        utils.concat([
          utils.zeroPad(utils.arrayify(this.token), 32),
          new Uint8Array([this.myIndex ^ 1]),
        ])
      )
      .then((signature) =>
        this.opponent.peer.send(PeerMessageType.GAME_OVER, {
          signature: utils.arrayify(signature),
        })
      )
      .then(() => this.modules.rzrDB.removeGameAndSignatures(this.token))
      .then(() => timer(1500))
      .then(() => this.emit("gameOver", true));
  };
}
