import { useRef, useEffect, FC, ReactNode } from "react";
import ReactDOM from "react-dom";
import styled, { css } from "styled-components";
import { Primitive } from "../Primitives/types";
import { Stack } from "../Primitives";

type ModalStyles = Primitive & {
  $size?: "lg" | "md" | "sm" | "auto";
};

const StyledModal = styled(Stack)<ModalStyles>`
  position: fixed;
  margin: auto;
  color: white;

  top: 1rem;
  left: 1rem;
  right: 1rem;
  min-height: 8rem;
  padding: 1rem;

  @media screen and (min-width: 1024px) {
    ${({ $size }) =>
      $size === "auto"
        ? css`
            top: 30%;
            left: 30%;
            right: 30%;
            padding: 1.5rem;
            max-width: 20rem;
          `
        : $size === "sm"
        ? css`
            top: 30%;
            height: 20%;
            left: 30%;
            right: 30%;
            padding: 1.5rem;
            max-width: 20rem;
          `
        : $size === "lg"
        ? css`
            top: 10%;
            height: 70%;
            left: 20%;
            right: 20%;
            padding: 2.5rem;
          `
        : css`
            top: 20%;
            min-height: 30%;
            left: 20%;
            right: 20%;
            padding: 2rem;
            max-width: 32rem;
          `}
  }

  ::after {
    z-index: -1;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    border: 3px solid #22acde;
    content: " ";
    clip-path: polygon(-50% 25%, 50% 150%, 160% 50%, 50% -25%);
  }
`;

const root = document.getElementById("root") as HTMLElement;

const Modal: FC<
  Primitive & ModalStyles & { className?: string; children: ReactNode }
> = ({ className, children, $size, $align, $justify, $gap }) => {
  const el = useRef(document.createElement("aside"));
  el.current.className = `modal${className ? ` ${className}` : ""}`;

  useEffect(() => {
    const element = el.current;
    root.prepend(element);

    return () => {
      root.removeChild(element);
    };
  }, []);

  return ReactDOM.createPortal(
    <StyledModal $size={$size} $gap={$gap} $justify={$justify} $align={$align}>
      {children}
    </StyledModal>,
    el.current
  );
};

export default Modal;
