const MAX_SYNC_THREADS = 1;

const log = (s: string, slot?: number) => logger.log(s, "RzR", slot);

// no rzrSync module
// This module is responsible for synchronousity
// sauron's eye peer seeking
// only in my domain
// distinguished by multiple parity algorithms to ensure network completeness
// remember bought sales are stored on chain
// they are publicly avaiable any time
// store them even if they are expired

type RzRConfiguration = {
  rzrSocket: {
    // this is only for initial contact, all signaling servers should be queried from here and stored in database
    rtc: RTCConfiguration;
  };
  ethereum: {
    contracts: {
      RzR: ContractConfig;
      RzRShards: ContractConfig;
    };
  };
};

type SupportedGames = "RzRShards";

export type EventMap = EthereumEventMap &
  RzRSocketEventMap &
  RzRDBEventMap &
  LogEventMap &
  ConnectionMatrixEventTypes &
  RzRContractEventMap & {
    peersChanged: string[];
    gamesChanged: Array<
      Game & {
        hasIdentity: boolean;
        hasSignature: boolean;
      }
    >;
    salesChanged: SaleDetails[];
    connectionStatusChanged: boolean;
  };

type Modules = {
  ethereum: Ethereum;
  rzrDB: RzRDB;
  rzrContract: RzRContract;
  rzrSync: RzRSync;
};

// also responsible for wiring
// rzr can eventually become something completely arbitrary
// an FPGA implementation
// or multiple instances on a raspberry PI
//
//
// add support for external modules

type RzRModulesConfiguration = {
  Ethereum: {
    provider: typeof Provider;
  };
  RzRDB: {
    backend: typeof Database;
  };
  RzRPeer: {
    supportedProtocols: CommunicationProtocols;
  };
};

export default class RzR extends Observable<EventMap> {
  exploredPeers = new Set<string>();

  config: RzRConfiguration;

  connectionMatrix: ConnectionMatrix;

  modules: Modules;
  activeGame: RzRShards | null = null;
  isOnline: boolean = false;
  syncThreadCount: number = 0;

  // load active game, in hook, make user be captive to the game

  constructor(
    config: RzRConfiguration,
    {
      RzRDB: { backend: DatabaseBackend },
      Ethereum: { provider: EthereumProvider },
      RzRPeer: { supportedProtocols },
    }: RzRModulesConfiguration
  ) {
    super();

    let slot = log("detecting environment...");
    // ^^ this will be taken care of outside
    // well.. sometimes
    // rzrsocket will use backend depending on target
    // so, how can we decide?

    this.config = config;

    const rzrDB = new RzRDB(DatabaseBackend);
    const ethereum = new Ethereum(rzrDB, EthereumProvider);

    this.connectionMatrix = new ConnectionMatrix(rzrDB, ethereum);

    const rzrContract = new RzRContract(
      this.config.ethereum.contracts.RzR,
      ethereum,
      rzrDB
    );

    setCommunicationProtocols(supportedProtocols);

    this.modules = {
      ethereum,
      rzrDB,
      rzrContract,
      // rzrSocket,
      rzrSync: new RzRSync(rzrDB, rzrContract),
    };

    log("done", slot);

    this.attachListeners();

    this.watchOnlineStatus();
  }

  // this could be part of rzrSocket
  private watchOnlineStatus = () => {
    if (typeof window === "undefined") return;

    const isOnline = window.navigator.onLine;

    if (isOnline !== this.isOnline) {
      this.emit("connectionStatusChanged", isOnline);
    }

    this.isOnline = isOnline;

    timer(5000).then(this.watchOnlineStatus);
  };

  private attachListeners() {
    const redirect =
      <K extends keyof EventMap>(type: K) =>
      (ev: EventMap[K]) =>
        super.emit(type, ev);

    // events
    this.modules.ethereum.addEventListener(
      "accountChanged",
      redirect("accountChanged")
    );

    this.modules.ethereum.addEventListener(
      "identityChainLoaded",
      redirect("identityChainLoaded")
    );

    logger.addEventListener("logs", redirect("logs"));

    this.modules.ethereum.addEventListener(
      "chainChanged",
      redirect("chainChanged")
    );

    this.modules.rzrContract.addEventListener(
      "accountLoaded",
      redirect("accountLoaded")
    );

    this.modules.rzrDB.addEventListener(
      "databaseLoaded",
      redirect("databaseLoaded")
    );

    this.modules.ethereum.addEventListener(
      "newSignature",
      redirect("newSignature")
    );

    this.modules.rzrContract.addEventListener(
      "pendingTransactionChanged",
      redirect("pendingTransactionChanged")
    );

    this.connectionMatrix.addEventListener(
      "rzrSocketConnectionChanged",
      redirect("rzrSocketConnectionChanged")
    );

    this.connectionMatrix.addEventListener("newPeerConnection", (peer) => {
      peer.addEventListener(PeerMessageType.INITIATE_DATA_TRANSFER, () =>
        this.modules.rzrSync.synchronizeUpstream(peer)
      );
    });

    this.modules.rzrDB.addEventListener(DataType.GAME, () =>
      this.loadGames().then((games) => this.emit("gamesChanged", games))
    );

    this.modules.rzrDB.addEventListener(DataType.SALE, () =>
      this.loadSales().then((sales) => this.emit("salesChanged", sales))
    );
  }

  // since peers are managed by this entity
  // this must stay here for now
  // once we have distributed peer management and handover
  // between modules
  // we can consider moving it
  private syncSequence = async (): Promise<void> => {
    if (this.syncThreadCount >= MAX_SYNC_THREADS) return;
    if (this.modules.ethereum.identity === null) return;
    this.syncThreadCount += 1;
    this.exploredPeers.add(this.modules.ethereum.address);

    const player = await this.modules.rzrDB.loadRandomPlayer(
      this.exploredPeers
    );

    const restartSequence = (t: number) => {
      this.syncThreadCount -= 1;
      if (player && this.activeGame?.opponent.peer.identity !== player.identity)
        timer(500).then(() =>
          this.connectionMatrix.releasePeer(player.address)
        );
      return timer(t).then(this.syncSequence);
    };

    if (!player) {
      log("no player found");
      if (this.exploredPeers.size > 0) {
        this.exploredPeers = new Set();
      }
      return restartSequence(60000);
    }

    const peer = await this.connectionMatrix.getOrCreatePeer(
      player.address,
      player.identity,
      IntentType.SYNC_DOWNSTREAM
    );

    this.exploredPeers.add(player.address);

    const result = await peer.connect();

    if (!result) {
      this.connectionMatrix.releasePeer(player.address);
      return restartSequence(2000);
    }

    await Promise.race([
      timer(30000), // give max 30 seconds
      this.modules.rzrSync.synchronizeDownstream(peer),
    ]);

    return restartSequence(60000);
  };

  // state queries
  get hasIdentity() {
    return this.modules.ethereum.identity !== null;
  }

  // workflows

  // these are redirect workflows, must check for target
  // if target was not me, send them to my peers
  // if one of my peers is in there, send it only to him
  // remember though, this is not enough
  // it's actually not addressAndOffer
  // but rather addressAndSourceAndOffer
  // there can be multiple relays
  // not really required in rzr node based routing
  private onAnswer = (addressAndOffer: Uint8Array) => {};

  private onCandidate = (addressAndOffer: Uint8Array) => {};

  initialize() {
    return this.modules.rzrDB.initialize().then(() => {
      this.modules.ethereum.initialize();

      once(this.modules.rzrContract, "contractLoaded").then(
        this.modules.rzrSync.syncOnChainData
      );

      Promise.all([
        once(this.modules.ethereum, "identityChainLoaded"),
        once(this.modules.rzrSync, "chainSyncFinished", {
          precondition: (success: boolean) => success,
        }),
        once(this.connectionMatrix, "rzrSocketConnectionChanged", {
          precondition: (isConnected) => isConnected,
        }),
      ]).then(this.syncSequence);
    });
  }

  buy(saleDetails: SaleDetails) {
    return this.modules.rzrContract.buy(saleDetails);
  }


  storeRouter(address: string, url: string) {
    return this.modules.rzrDB.storeRouter({ address, url });
    // .then(this.modules.rzrSocket.connect);
  }

  // be extremely careful with this
  // reveals the entire identity chain, only reveal on special request
  revealIdentity() {
    return this.modules.rzrDB.loadIdentityChain(this.modules.ethereum.address).then(identity => {
      if (identity === null) return null

      return utils.hexlify(utils.concat([
        utils.zeroPad(utils.arrayify(identity.latestIdentityIndex), 2),
        identity.identityRoot
      ]))
    })
  }

  storeIdentity(identity: string) {
    if (identity.length !== 70) return Promise.resolve(false);

    return this.modules.rzrDB
      .storeIdentityChain(
        this.modules.ethereum.address,
        utils.arrayify(identity)
      )
      .then(this.modules.ethereum.reloadIdentity);

    // .then(this.modules.rzrSocket.connect);
  }

  // maybe could have its own dedicated module
  // basically all workflows could work alongside RzR
  // they would be a "worker" / "workflow" module
  // all exposed actions, would eventually end up here though anyway
  // anything that is not closely related to
  async createSale({ expiry, assets, price }: NewSale) {
    const assetsAndToken = utils.concat([
      utils.zeroPad(utils.arrayify(expiry), 2),
      utils.zeroPad(utils.arrayify(Math.floor(new Date().getTime() / 1000)), 8),
      utils.randomBytes(2),
      utils.zeroPad(utils.arrayify(assets), 20),
    ]);

    const priceBytes = utils.zeroPad(utils.arrayify(price), 32);

    const signature = utils.arrayify(
      await this.modules.ethereum.requestSignature(
        utils.concat([assetsAndToken, priceBytes]),
        {
          title: "New Sale",
          description: `You are signing that anybody can purchase assets for ${expiry} minutes from now`,
        }
      )
    );

    await this.modules.rzrDB.storeSale(
      digestSale(
        utils.concat([
          assetsAndToken,
          priceBytes,
          signature,
          this.modules.ethereum.address,
        ])
      )
    );
  }

  // could be just game type
  play = (gameName: SupportedGames, value: BigNumber, useNewIdentity = false) =>
    this.modules.rzrDB
      .loadGame(utils.hexlify(value))
      .then((game) => {
        if (game === null) return null;
        return this.modules.rzrDB.loadPlayer(game.players[0]);
      })
      .then((player) => {
        if (!player) return null;

        const peer = this.connectionMatrix.getOrCreatePeer(
          player.address,
          player.identity,
          IntentType.PLAY_GAME
        );

        return peer.connect();
      })
      .then((isConnectedOrDoesNotExist) => {
        if (isConnectedOrDoesNotExist === false)
          throw new Error("opponent not available");

        return isConnectedOrDoesNotExist === null ? true : false;
      })
      .then((forceKeepIdentity) =>
        this.modules.rzrContract.play(
          this.config.ethereum.contracts[gameName].address,
          value,
          forceKeepIdentity && useNewIdentity
        )
      );

  claimVictory = async (token: BigNumber) => {
    const signature = await this.modules.rzrDB.loadSignature(
      token.toHexString()
    );

    if (!signature) return;

    return this.modules.rzrContract.claimVictory(
      token,
      new Uint8Array(signature)
    );
  };

  mergeAssets = (assetsFrom: BigNumber, assetTo: BigNumber) => {
    return this.modules.rzrContract.mergeAssets(assetsFrom, assetTo);
  };

  loadSales = () => {
    return this.modules.rzrDB.loadSales();
  };

  loadGames = () => {
    return this.modules.rzrDB
      .loadGames()
      .then(({ games, signatures }) =>
        games.map((game) => ({
          ...game,
          hasIdentity: game.players.includes(this.modules.ethereum.address),
          hasSignature: signatures.includes(game.token.toHexString()),
        }))
      )
      .then((games) =>
        games.filter(
          (game) =>
            game.hasIdentity ||
            (game.players.length === 1 &&
              game.players[0] !== this.modules.ethereum.address) ||
            game.hasSignature
        )
      );
  };

  /*
   * a slightly more complicated workflow
   * loads identity
   * connects to peer
   * initiates game
   * */
  async loadGame(token: string): Promise<RzRShards> {
    if (this.activeGame) return Promise.resolve(this.activeGame);
    const game = await this.modules.rzrDB.loadGame(token);

    if (
      !game ||
      !game.players.includes(this.modules.ethereum.address.toLowerCase())
    )
      throw new Error("game not found");

    if (game.gameAddress !== this.config.ethereum.contracts.RzRShards.address)
      throw new Error("game not supported");

    // TODO: if there is existing active game, remove peer from there

    const opponent = (await this.modules.rzrDB.loadPlayer(
      game.players.find(
        (player) => player !== this.modules.ethereum.address
      ) as string
    )) as Player;

    const peer = this.connectionMatrix.getOrCreatePeer(
      opponent.address,
      opponent.identity,
      IntentType.PLAY_GAME
    );

    await peer.connect();

    this.activeGame = new RzRShards(
      this.config.ethereum.contracts.RzRShards,
      token,
      peer,
      this.modules.rzrContract,
      this.modules.ethereum,
      this.modules.rzrDB
    );

    return this.activeGame;
  }

  closeGame(token: string) {
    if (!this.activeGame) return;
    const opponent = this.activeGame.opponent.address;
    this.activeGame = null;
    this.connectionMatrix.releasePeer(opponent);
  }
}

