import { useEffect, useState, ReactNode, Fragment } from "react";
import styled, { css, FlattenSimpleInterpolation } from "styled-components";
import Triangle, { StyledTriangle } from "../../lvl1/Triangle";

export type ResponsiveGridDimension = [number, number, number];

interface IRow {
  isEven: boolean;
}

interface IGrid {
  cols: number;
  isOdd: boolean;
  gap?: number;
}

const Grid = styled.div<IGrid>`
  padding-left: calc(var(--triangle-size) * 0.57735026919);
  display: inline-grid;
  grid-template-columns: ${({ cols }) =>
    [...new Array(cols)].map(() => "1fr ")};

  > span {
    display: none;
  }

  > div,
  > label {
    margin-left: calc(var(--triangle-size) * -0.57735026919 + ${({gap}) => gap}px);
    margin-bottom: ${({gap}) => gap}px;
  }

  ${StyledTriangle}:nth-child(${({isOdd}) => isOdd ? 'odd': 'even'}) {
    transform: rotate(180deg);
    transform-origin: center;
  }
`;

const Row = styled.div<IRow>`
  display: flex;
  overflow: hidden;

  > div,
  > label {
    margin-left: var(--triangle-margin-offset);
  }

  > ${StyledTriangle}:nth-child(${({ isEven }) => (isEven ? "even" : "odd")}) {
    transform: rotate(180deg);
    transform-origin: center;
  }
`;

const getViewportIndex = (width: number) =>
  width < 720 ? 0 : width < 1440 ? 1 : 2;

type IUseBoardGrid = {
  cols: ResponsiveGridDimension;
  rows: ResponsiveGridDimension;
};

const useBoardGrid = ({ cols, rows }: IUseBoardGrid) => {
  const [viewportIndex, setViewportIndex] = useState(0);

  useEffect(() => {
    const handleResize = () => {
      const _viewportIndex = getViewportIndex(window.innerWidth);
      if (viewportIndex !== _viewportIndex) {
        setViewportIndex(_viewportIndex);
      }
    };
    window.addEventListener("resize", handleResize);
    handleResize();
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, [viewportIndex]);

  return {
    cols: cols[viewportIndex],
    rows: rows[viewportIndex],
    viewportIndex,
  };
};

export type Position = null | [number, number];

export type Element = {
  isFullSize?: boolean;
  content?: ReactNode;
  position: [Position, Position, Position];
  action?: () => void;
  styles?: string | FlattenSimpleInterpolation;
};

interface IBoard {
  elements: Element[];
  responsiveCols: ResponsiveGridDimension;
  responsiveRows: ResponsiveGridDimension;
  isOdd?: boolean;
  gap?: number;
}

const Board: React.FC<IBoard> = ({
  elements,
  responsiveCols,
  responsiveRows,
  isOdd = false,
  gap=0
}) => {
  const { cols, rows, viewportIndex } = useBoardGrid({
    cols: responsiveCols,
    rows: responsiveRows,
  });

  return (
    <Grid cols={cols} isOdd={isOdd} gap={gap}>
      {[...new Array(rows * cols)].map((_, i) => (
        <Fragment key={i}>
          {(cols & 0x1) === 0 && (i + 1) % cols === 0 && <span />}
          <Triangle
            element={elements.find(
              (e) =>
                e.position[viewportIndex] !== null &&
                (e.position[viewportIndex] as [number, number])[1] ===
                  Math.floor(i / cols) &&
                (e.position[viewportIndex] as [number, number])[0] === i % cols
            )}
          />
        </Fragment>
      ))}
    </Grid>
  );
};

export default Board;
