import localConfig from "./local";
import productionConfig from "./production";

type ContractConfig = {
  address: string;
  abi: any;
};

let config: {
  rzrSocket: {
    rtc: {
      iceServers: Array<{
        urls: string;
      }>;
    };
  };
  ethereum: {
    contracts: {
      RzR: ContractConfig;
      RzRShards: ContractConfig;
    };
  };
};

if (process.env.NODE_ENV === "production") {
  config = productionConfig;
} else {
  config = localConfig;
}

export default config;
