import {
  logger,
  Database,
  RawGame,
  SerializedSale,
  Player,
  Router,
} from "../../../rzr-module/RzR";

const log = (s: string, slot?: number) => logger.log(s, "IndexedDB", slot);

export default class IndexedDBBackend extends Database {
  database: IDBDatabase | null = null;

  initialize() {
    const request = window.indexedDB.open("RzR", 10);
    let slot = log("opening local database...");

    return new Promise<boolean>((resolve, reject) => {
      request.onsuccess = async (event) => {
        log("done", slot);
        this.database = (event.target as IDBOpenDBRequest).result;
        this.emit("databaseLoaded", true);
        resolve(true);
      };

      request.onerror = () => {
        this.emit("databaseLoaded", false);
        log("failed", slot);
        reject();

        // TODO: add somehow terminal errors
      };

      request.onupgradeneeded = (event) => {
        const slot = log("purging local database...");
        const db = (event.target as IDBOpenDBRequest).result;
        try {
          db.deleteObjectStore("sales");
          db.deleteObjectStore("games");
          db.deleteObjectStore("tokens");
          db.deleteObjectStore("signatures");
          db.deleteObjectStore("identities");
          db.deleteObjectStore("expiredSales");
        } catch (e) {}

        db.createObjectStore("games", { keyPath: "token" });
        const salesStore = db.createObjectStore("sales");
        salesStore.createIndex("seller", "address", { unique: false });
        salesStore.createIndex("createdAt", "timestamp", {
          unique: false,
        });
        salesStore.createIndex("signer", "identity", {
          unique: false,
        });

        const eventsStore = db.createObjectStore("events", { keyPath: "transactionHash" });
        eventsStore.createIndex("blockNumber", "blockNumber", {
          unique: false,
        });

        // all stored, unused private keys
        db.createObjectStore("signatures");
        db.createObjectStore("identities");
        const routerStore = db.createObjectStore("routers", {
          keyPath: "address",
        });
        this.launchRequest(
          routerStore.add({
            address: "0x9dfb4bfa6be6beaff1d0be20ee575b4a094d3387",
            url: "ws://localhost:8080/signal",
          })
        );
        // it's technically identities for other players
        // should store latest two
        // through PING the requested identity should be sent over
        const playersStore = db.createObjectStore("players", {
          keyPath: "address",
        });
        playersStore.createIndex("createdAtBlock", "blockNumber", {
          unique: false,
        });
        playersStore.createIndex("latestIdentity", "identity", {
          unique: true,
        });
        db.createObjectStore("gamesData");
        log("done", slot);
      };

      /*
      // this is for ongoing stuff during development
      request.onupgradeneeded = (event) => {
        const slot = log("upgrading local database...");
        const db = (event.target as IDBOpenDBRequest).result;
        const eventsStore = db.createObjectStore("events", { keyPath: "hash" });
        eventsStore.createIndex("blockNumber", "blockNumber", {
          unique: false,
        });
        /*
        const request = event.target as IDBOpenDBRequest;
        const tx = request.transaction;
        if (!tx) return;
        tx.objectStore("sales").createIndex("signer", "identity", {
          unique: false,
        });

        log("done", slot);
      };*/
    });
  }

  private getDatabase() {
    if (!this.database) throw new Error("database not loaded yet");
    return this.database;
  }

  private async launchRequest(request: IDBRequest): Promise<IDBRequest> {
    return new Promise((resolve, reject) => {
      request.onsuccess = () => {
        resolve(request);
      };
      request.onerror = () => {
        reject(request);
      };
    });
  }

  async purgeExpiredSales(beforeTimestamp: number): Promise<boolean> {
    const objectStore = await this.openObjectStore("sales", "readwrite");

    await this.launchRequest(
      objectStore.delete(IDBKeyRange.upperBound(beforeTimestamp))
    );

    return true;
  }

  async removeAllSalesByIdentity(identity: string): Promise<boolean> {
    const objectStore = await this.openObjectStore("sales", "readwrite");

    return new Promise((r) => {
      objectStore
        .index("signer")
        .openCursor(IDBKeyRange.only(identity)).onsuccess = (event: any) => {
        const cursor = event.target.result;

        if (cursor) {
          cursor.delete();
          cursor.continue();
        } else {
          r(true);
        }
      };
    });
  }

  async purgeExpiredEvents(blockLimit: number): Promise<boolean> {
    const objectStore = await this.openObjectStore("events", "readwrite");

    return new Promise((r) => {
      objectStore
        .index("blockNumber")
        .openCursor(IDBKeyRange.upperBound(blockLimit)).onsuccess = (
        event: any
      ) => {
        const cursor = event.target.result;

        if (cursor) {
          cursor.delete();
          cursor.continue();
        } else {
          r(true);
        }
      };
    });
  }

  async purgeExpiredPlayers(blockLimit: number): Promise<boolean> {
    const objectStore = await this.openObjectStore("players", "readwrite");

    return new Promise((r) => {
      objectStore
        .index("createdAtBlock")
        .openCursor(IDBKeyRange.upperBound(blockLimit)).onsuccess = (
        event: any
      ) => {
        const cursor = event.target.result;

        if (cursor) {
          cursor.delete();
          cursor.continue();
        } else {
          r(true);
        }
      };
    });
  }

  async openObjectStore(
    storeName: string,
    accessType?: "readwrite" | "readonly"
  ) {
    if (!this.database) throw new Error("database not initialized");
    const transaction = this.database.transaction([storeName], accessType);

    return transaction.objectStore(storeName);
  }

  loadLatestBlock() {
    return Promise.resolve(parseInt(localStorage.latestBlock) || 0);
  }

  storeLatestBlock(blockNumber: number): Promise<boolean> {
    localStorage.setItem("latestBlock", blockNumber.toString());

    return Promise.resolve(true);
  }

  async removeGame(token: string): Promise<boolean> {
    const objectStore = await this.openObjectStore("games", "readwrite");

    await this.launchRequest(objectStore.delete(token));

    return true;
  }

  async storeGame(game: RawGame): Promise<boolean> {
    const objectStore = await this.openObjectStore("games", "readwrite");

    await this.launchRequest(objectStore.put(game));

    return true;
  }

  async loadIdentities() {
    const objectStore = await this.openObjectStore("identities", "readonly");

    const { result } = await this.launchRequest(objectStore.getAllKeys());

    return result;
  }

  async loadTokensWithIdentity() {
    const objectStore = await this.openObjectStore("identities", "readonly");

    const { result } = await this.launchRequest(objectStore.getAllKeys());

    return result;
  }

  async loadSignatures() {
    const objectStore = await this.openObjectStore("signatures", "readonly");

    const { result } = await this.launchRequest(objectStore.getAllKeys());

    return result;
  }

  async loadGames() {
    const objectStore = await this.openObjectStore("games", "readonly");

    const { result } = await this.launchRequest(objectStore.getAll());

    return result;
  }

  async loadSignature(token: string): Promise<ArrayBuffer | null> {
    // const slot = log(`verifying sale ${token}...`);
    const objectStore = await this.openObjectStore("signatures", "readonly");

    const { result: signature } = await this.launchRequest(
      objectStore.get(token)
    );
    // log("done", slot);

    if (!signature) return null;

    return signature;
  }

  async loadSale(token: number): Promise<SerializedSale | null> {
    const objectStore = await this.openObjectStore("sales", "readonly");

    const { result: value } = await this.launchRequest(objectStore.get(token));

    if (!value) return null;

    return value;
  }

  async loadSales(): Promise<SerializedSale[]> {
    const objectStore = await this.openObjectStore("sales", "readonly");

    const { result: values } = await this.launchRequest(objectStore.getAll());

    return values;
  }

  async loadRouters(): Promise<Router[]> {
    const objectStore = await this.openObjectStore("routers", "readonly");

    const { result: routers } = await this.launchRequest(objectStore.getAll());

    return routers;
  }

  async loadRouter(address: string): Promise<Router | null> {
    const objectStore = await this.openObjectStore("routers", "readonly");

    const { result: value } = await this.launchRequest(
      objectStore.get(address)
    );

    if (!value) return null;

    return value;
  }

  async loadPlayers(): Promise<Player[]> {
    const objectStore = await this.openObjectStore("players", "readonly");

    const { result: players } = await this.launchRequest(objectStore.getAll());

    return players;
  }

  async storeIdentity(
    chainId: string,
    privateKey: ArrayBuffer
  ): Promise<boolean> {
    const objectStore = await this.openObjectStore("identities", "readwrite");

    await this.launchRequest(objectStore.put(privateKey, chainId));

    return true;
  }

  async loadIdentityChain(address: string): Promise<ArrayBuffer | null> {
    const objectStore = await this.openObjectStore("identities", "readonly");

    const { result: value } = await this.launchRequest(
      objectStore.get(address)
    );

    if (!value) {
      return null;
    }

    return value;
  }

  async hasEventBeenProcessed(transactionHash: string): Promise<boolean> {
    const objectStore = await this.openObjectStore("events", "readonly");

    const { result: event } = await this.launchRequest(
      objectStore.get(transactionHash)
    );

    return event ? true : false;
  }

  async loadPlayer(address: string): Promise<Player | null> {
    const objectStore = await this.openObjectStore("players", "readonly");

    const { result: player } = await this.launchRequest(
      objectStore.get(address)
    );

    if (!player) {
      return null;
    }

    return player;
  }

  async loadPlayerByIdentity(identity: string): Promise<Player | null> {
    const objectStore = await this.openObjectStore("players", "readonly");
    const { result } = await this.launchRequest(
      objectStore.index("latestIdentity").openCursor(IDBKeyRange.only(identity))
    );

    if (result) return result.value;

    return null;
  }

  async loadGame(token: string): Promise<RawGame | null> {
    const objectStore = await this.openObjectStore("games", "readonly");

    const { result: value } = await this.launchRequest(objectStore.get(token));

    if (!value) {
      return null;
    }

    return value;
  }

  async loadGameData(token: string): Promise<ArrayBuffer | null> {
    const objectStore = await this.openObjectStore("gamesData", "readonly");

    const { result: value } = await this.launchRequest(objectStore.get(token));

    return value;
  }

  async storeSignature(
    token: string,
    signature: ArrayBuffer
  ): Promise<boolean> {
    const objectStore = await this.openObjectStore("signatures", "readwrite");

    await this.launchRequest(objectStore.add(signature, token));

    return true;
  }

  async storeGameData(token: string, gameData: ArrayBuffer): Promise<boolean> {
    const objectStore = await this.openObjectStore("gamesData", "readwrite");

    await this.launchRequest(objectStore.put(gameData, token));

    return true;
  }

  async removeIdentity(token: string): Promise<boolean> {
    const objectStore = await this.openObjectStore("identities", "readwrite");

    await this.launchRequest(objectStore.delete(token));

    return true;
  }

  async removeGameData(token: string): Promise<boolean> {
    const objectStore = await this.openObjectStore("gamesData", "readwrite");

    await this.launchRequest(objectStore.delete(token));

    return true;
  }

  async removeSignature(token: string): Promise<boolean> {
    const objectStore = await this.openObjectStore("signatures", "readwrite");

    await this.launchRequest(objectStore.delete(token));

    return true;
  }

  async storeSale(
    expiry: number,
    serializedSale: SerializedSale
  ): Promise<boolean> {
    const objectStore = await this.openObjectStore("sales", "readwrite");

    await this.launchRequest(objectStore.put(serializedSale, expiry));

    return true;
  }

  async storeEvent(
    blockNumber: number,
    transactionHash: string
  ): Promise<boolean> {
    const objectStore = await this.openObjectStore("events", "readwrite");

    await this.launchRequest(
      objectStore.put({ blockNumber, transactionHash })
    );

    return true;
  }

  async storePlayer(player: Player): Promise<boolean> {
    const objectStore = await this.openObjectStore("players", "readwrite");

    await this.launchRequest(objectStore.put(player));

    return true;
  }

  async storeRouter(router: Router): Promise<boolean> {
    const objectStore = await this.openObjectStore("routers", "readwrite");

    await this.launchRequest(objectStore.put(router));

    return true;
  }

  async loadAllSalesFromAddress(address: string): Promise<SerializedSale[]> {
    const objectStore = await this.openObjectStore("sales", "readonly");
    const results = await new Promise<SerializedSale[]>((r) => {
      let sales: SerializedSale[] = [];
      objectStore
        .index("seller")
        .openCursor(IDBKeyRange.only(address)).onsuccess = (event: any) => {
        const cursor = event.target.result;

        if (cursor) {
          sales.push(cursor.value);
          cursor.continue();
        } else {
          r(sales);
        }
      };
    });

    return results;
  }
}
