import { useContext, useState, useEffect } from "react";
import styled from "styled-components";
import OpenGame from "../lvl2/OpenGame";
import { ActionLink, ActionButton } from "../lvl1/Button";
import { H2 } from "../lvl1/Typography";
import Modal, { CloseIcon } from "../lvl1/Modal";
import Page from "../lvl1/Page";
import { Input } from "../lvl1/Form";
import { Inline, Stack } from "../lvl1/Primitives";
import { utils, BigNumber, Wallet } from "ethers";
import RzRContext from "../../contexts/RzR";
import rzr from "../../rzr";
import useWatchRzREvent from "../../hooks/useWatchRzREvent";

const GameContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  @media screen and (min-width: 720px) {
    justify-content: flex-start;
  }
`;

type FormElement = HTMLFormElement & {
  elements: {
    wager: HTMLInputElement;
    identity?: HTMLInputElement;
  };
};

const NewGameModal: React.FC<{ close: () => void }> = ({ close }) => {
  const onSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const wager = (e.target as FormElement).elements.wager.value;
    const createNewIdentity = (e.target as FormElement).elements.identity?.checked;
    rzr.play("RzRShards", utils.parseUnits(wager, "ether"), createNewIdentity);
    close();
  };
  return (
    <Modal $size="sm" $justify="stretch">
      <Stack onSubmit={onSubmit} $justify="space-between" as="form" $expand>
        <Inline $justify="space-between">
          <H2 $font="secondary">New game</H2>
          <CloseIcon onClick={close} />
        </Inline>
        <Inline $justify="center" as="label">
          <span>Wager</span>
          <Input name="wager" />
        </Inline>
        {rzr.hasIdentity && (
          <Inline $justify="center" as="label">
            <span>New Identity</span>
            <input type="checkbox" name="identity" />
          </Inline>
        )}
        <Inline $justify="flex-end">
          <ActionButton $primary>create</ActionButton>
        </Inline>
      </Stack>
    </Modal>
  );
};

/*
 * Always open, static game.
 * */
const token = "0x01634578c0cc9ab0";

const Play = () => {
  const [isNewGameModalOpen, setIsNewGameModal] = useState<boolean>(false);
  const [games, setGames] = useWatchRzREvent<"gamesChanged">(
    "gamesChanged",
    [],
    null,
    rzr.loadGames
  );
  const [opponent, setOpponent] = useState<string>("");

  const { clients } = useContext(RzRContext);

  const createSignature = async () => {
    const pk = localStorage.pk;
    if (pk) {
      const player = new Wallet(utils.arrayify(pk));
      console.log(player.address);
      return player.signMessage(
        utils.concat([
          utils.zeroPad(utils.arrayify(BigNumber.from(token)), 32),
          new Uint8Array([0]),
        ])
      );
    }
  };

  /*
  useEffect(() => {
    createSignature().then((s) => console.log(s));
    const onPeer = (peer: string) => {
      if (peer !== opponent) return;
      createSignature().then((s: any) => rzrSocket.sendToPeer(opponent, s));
    };
    rzrSocket.addEventListener("newPeerConnection", onPeer);
    return () => {
      rzrSocket.removeEventListener("newPeerConnection", onPeer);
    };
    }, [opponent]);
   */

  const handleClickPlay = (player: string, wager: BigNumber) => {
    // TODO: piloting the idea of not generating a new identity upon secondary connection
    // this gives us the ability to immediately connect to the target peer
    // rendering them unavailable to accept more connections.
    //
    // game intent happens before tx is submitted
    //
    // only if permission is given, do we send the transaction
    //
    // once the tx has completed (we are both waiting for it)
    //
    // we can go to step 2
    //
    // oor, we can actually go to the lobby and select the cards,
    // also chat etc..
    rzr.play("RzRShards", wager).then(() => {
      /*
      if (rzrSync.activePeer && rzrSync.activePeer.address !== player) {
        rzrSocket.openConnection(utils.arrayify(player));
        }*/
      setOpponent(player);
    });
  };

  const cv = () => {
    /*
    handleClick(
      '0x77075c928b15a804519068663f1a780b9eb55555',
      BigNumber.from(token)
      )*/

    rzr.claimVictory(BigNumber.from("0x01634578c0cc9ab0"));
    //rzrSocket.createOffer(utils.arrayify(clients[0]))
  };
  // TODO: should display games with one player where I have identity, as pending with disabled button
  return (
    <Page
      title="Lounge"
      cta={
        <ActionButton $primary onClick={() => setIsNewGameModal(true)}>
          New Game
        </ActionButton>
      }
    >
      <button onClick={cv}></button>
      {isNewGameModalOpen && (
        <NewGameModal close={() => setIsNewGameModal(false)} />
      )}
      <p>
        All available games are shown below, games where the reward is +4 blade
        are played without blades.
      </p>

      <GameContainer>
        {games.map((game) => (
          <OpenGame
            key={game.token.toString()}
            wager={game.token}
            player={game.players[0]}
            action={
              game.hasSignature ? (
                <ActionButton onClick={() => rzr.claimVictory(game.token)}>
                  Claim
                </ActionButton>
              ) : game.players.length === 2 ? (
                <ActionLink to={`/game/${game.token.toHexString()}`}>
                  Jump
                </ActionLink>
              ) : !game.hasIdentity ? (
                <ActionButton
                  onClick={() => handleClickPlay(game.players[0], game.token)}
                >
                  Play
                </ActionButton>
              ) : (
                "PENDING"
              )
            }
          />
        ))}
      </GameContainer>
      {/*
      <ActionContainer>
        <ActionButton disabled>Checkout</ActionButton>
        </ActionContainer>*/}
    </Page>
  );
};

export default Play;
