import { BigNumber, utils, Event } from "ethers";
import Observable from "./Observable";
import RzRPeer, { PeerMessageType, IntentType, IntentStatus } from "./RzRPeer";
import RzRDB, { getExpiry } from "./RzRDB";
import logger from "./Log";
import { sequentialProcess, digestSale, compressSale, timer } from "./utils";
import { SaleDetails, Player } from "./types";
import RzRContract, {
  Events,
  NewPlayerEvent,
  NewIdentityEvent,
  AssetsBoughtEvent,
} from "./Ethereum/contracts/RzR";

const log = (s: string, slot?: number) => logger.log(s, "RzRSync", slot);
const _log = logger._log("RzRSync");

// this figure should fluctuate based on the number of sales it's processing
const SALE_VERIFICATION_RATE = 0.1;

type RzRSyncEventMap = {
  chainSyncFinished: boolean;
};

// All workflows related to data synchronization
export default class RzRSync extends Observable<RzRSyncEventMap> {
  modules: {
    rzrDB: RzRDB;
    rzrContract: RzRContract;
  };

  lastOnChainSync: number = 0;
  isSyncing = false;

  constructor(rzrDB: RzRDB, rzrContract: RzRContract) {
    super();

    this.modules = {
      rzrDB,
      rzrContract,
    };

    this.attachListeners();
  }

  attachListeners() {
    this.modules.rzrContract.addEventListener(
      Events.ASSETS_BOUGHT,
      this.onAssetsBought
    );
    this.modules.rzrContract.addEventListener(
      Events.NEW_PLAYER,
      this.onNewPlayer
    );
    this.modules.rzrContract.addEventListener(
      Events.NEW_IDENTITY,
      this.onNewIdentity
    );
  }

  // store all events that we've processed
  // it would be a bit wasteful, but let's think
  // but it's not really avoidable?
  // it's actually fine. store those events
  // and at next sync we'll just avoid them!
  private storeEvent = (event: Event) =>
    this.modules.rzrDB.storeEvent(event.blockNumber, event.transactionHash);

  private onAssetsBought = ({ assetsAndToken, event }: AssetsBoughtEvent) =>
    this.modules.rzrDB
      .flagSaleAsUsed(utils.arrayify(assetsAndToken))
      .then(() => this.storeEvent(event));

  private onNewPlayer = ({ token, player, game, event }: NewPlayerEvent) =>
    this.modules.rzrDB
      .storeOrDeleteGame(utils.hexlify(token), player.toLowerCase(), game)
      .then(() => this.storeEvent(event));

  private onNewIdentity = ({
    event,
    identity,
    address,
    previousKey,
  }: NewIdentityEvent) => {
    this.modules.rzrDB.storePlayer({
      address,
      identity,
      blockNumber: event.blockNumber,
      previousIdentity: previousKey,
    });
  };

  synchronizeDownstream = (peer: RzRPeer): Promise<boolean> => {
    const failureCount = 0;
    // both methods should return with a boolean and update failurecount accordingly
    // this is actually a nice little piece of code, or is it?
    if (!peer.isConnected) {
      return new Promise((resolve) => {
        peer.addEventListener("connectionStatusChanged", (isConnected) => {
          if (!isConnected) return;
          this.synchronizeDownstream(peer).then(resolve);
        });
      });
    }

    const onNewSale = async (sale: SaleDetails) => {
      // TODO: drop after 3 failures
      //
      //
      // could use a new schema here
      //
      // also check expiry requirements (sale should not be longer than 2 weeks)

      const token = utils.concat([
        utils.zeroPad(utils.arrayify(sale.expiry), 2),
        utils.zeroPad(utils.arrayify(sale.timestamp), 8),
        utils.zeroPad(utils.arrayify(sale.r), 2),
        utils.zeroPad(utils.arrayify(sale.assets), 20),
      ]);

      const seller = utils
        .verifyMessage(
          utils.concat([token, utils.zeroPad(utils.arrayify(sale.price), 32)]),
          sale.signature
        )
        .toLowerCase();

      if (await this.modules.rzrDB.loadSale(getExpiry(sale))) return;

      const player = await this.modules.rzrDB.loadPlayer(sale.address);

      if (player === null || seller !== player.identity) return;

      const shouldVerify = Math.random() < SALE_VERIFICATION_RATE;

      if (!shouldVerify) {
        this.modules.rzrDB.storeSale(sale);
        return;
      }

      // also verify wether it is still active
      if (await this.modules.rzrContract.expired(BigNumber.from(token))) return;

      // get address assets
      const [assets, activeSalesAssets] = await Promise.all([
        this.modules.rzrContract.getAssets(sale.address),
        this.modules.rzrDB.loadAllSalesAssets(sale.address),
      ]);

      // to check for eligibility
      if (
        activeSalesAssets.reduce(
          (isEligible: boolean, assetCount: number, i: number) => {
            if (false) return true;
            if (assetCount > assets[i]) return true;
            return false;
          },
          false
        )
      )
        return;

      this.modules.rzrDB.storeSale(sale, true);
    };

    peer.updateIntent(IntentType.SYNC_DOWNSTREAM, IntentStatus.IN_PROGRESS);

    const slot = log("data transfer (downstream) in progress...");

    // TODO: need to sync routers
    peer.send(PeerMessageType.INITIATE_DATA_TRANSFER);
    peer.addEventListener(PeerMessageType.NEW_SALE, onNewSale);

    return new Promise((resolve) => {
      const onFinish = () => {
        log(
          `completed (received ${(peer as RzRPeer).receivedBytes} bytes)`,
          slot
        );
        peer.updateIntent(IntentType.SYNC_DOWNSTREAM, IntentStatus.COMPLETED);
        resolve(true);
      };

      peer.addEventListener(PeerMessageType.DATA_TRANSFER_COMPLETE, onFinish);
    });
  };

  synchronizeUpstream = async (peer: RzRPeer): Promise<boolean> => {
    const slot = log("data transfer (upstream) in progress...");

    peer.updateIntent(IntentType.SYNC_UPSTREAM, IntentStatus.IN_PROGRESS);

    return this.modules.rzrDB
      .loadSales()
      .then(async (sales: SaleDetails[]) => {
        for (let i = 0; i < sales.length; ++i) {
          await Promise.all([
            peer.send(PeerMessageType.NEW_SALE, sales[i]),
            timer(250),
          ]);
        }
      })
      .then(this.modules.rzrDB.loadRouters.bind(this.modules.rzrDB))
      .then(async (routers) => {
        for (let i = 0; i < routers.length; ++i) {
          await Promise.all([
            peer.send(PeerMessageType.NEW_ROUTER, routers[i]),
            timer(250),
          ]);
        }
      })
      .then(() => {
        peer.send(PeerMessageType.DATA_TRANSFER_COMPLETE);
        peer.updateIntent(IntentType.SYNC_UPSTREAM, IntentStatus.COMPLETED);
        log(`completed (shared ${peer.transferredBytes} bytes)`, slot);
        return true;
      });
  };

  syncOnChainData = async () => {
    if (
      this.isSyncing ||
      this.lastOnChainSync + 30 * 1000 > new Date().getTime()
    )
      return;
    this.isSyncing = true;
    const latestBlock = await this.modules.rzrDB.loadLatestBlock();

    let highestPurchaseBlock = latestBlock;
    let highestGameBlock = latestBlock;

    const processEvent = (
      event: Event,
      processor: (eventArgs: any) => Promise<any>
    ) => {
      if (!event.args) return Promise.resolve(null);
      highestPurchaseBlock = event.blockNumber;
      return this.modules.rzrDB
        .hasEventBeenProcessed(event.transactionHash)
        .then((hasBeenProcessed) =>
          hasBeenProcessed ? null : processor(event.args as any)
        );
    };

    const processPurchaseEvent = (event: Event) =>
      processEvent(event, () =>
        this.modules.rzrDB.flagSaleAsUsed(utils.arrayify(event.data))
      );

    const processNewPlayerEvent = (event: Event) =>
      processEvent(event, (args) =>
        this.modules.rzrDB.storeOrDeleteGame(
          args.token.toHexString(),
          args.player.toLowerCase(),
          args.game.toLowerCase()
        )
      );

    const processNewIdentityEvent = (event: Event) =>
      processEvent(event, (args) =>
        this.modules.rzrDB.storePlayer({
          address: args.player.toLowerCase(),
          identity: args.identity.toLowerCase(),
          previousIdentity: args.previousIdentity,
          blockNumber: event.blockNumber,
        })
      );

    const processPurchaseEvents = (events: Event[]) =>
      sequentialProcess(events, processPurchaseEvent);

    const processNewPlayerEvents = (events: Event[]) =>
      sequentialProcess(events, processNewPlayerEvent);

    const processNewIdentityEvents = (events: Event[]) =>
      sequentialProcess(events, processNewIdentityEvent);

    await _log(
      "synchronizing on chain data",
      Promise.all([
        this.modules.rzrContract
          .getPurchases(latestBlock + 1)
          .then(processPurchaseEvents),
        this.modules.rzrContract
          .getNewPlayers(latestBlock + 1)
          .then(processNewPlayerEvents),
        this.modules.rzrContract
          .getNewIdentities(latestBlock + 1)
          .then(processNewIdentityEvents),
      ])
    );

    this.lastOnChainSync = new Date().getTime();

    return this.modules.rzrDB
      .storeLatestBlock(
        highestPurchaseBlock > highestGameBlock
          ? highestPurchaseBlock
          : highestGameBlock
      )
      .then(() => {
        this.emit("chainSyncFinished", true);
      })
      .catch(() => {
        this.emit("chainSyncFinished", false);
      })
      .finally(() => {
        this.isSyncing = false;
      });
  };
}
