import { BigNumber } from "ethers";
import { Schema, SaleDetails, AppliedSchema, Router } from "../types";
import constants from "../constants";

export enum PeerMessageType {
  INITIATE_DATA_TRANSFER = 10,
  NEW_SALE,
  NEW_ROUTER,
  //NEW_SALE_CLOSE,
  DATA_TRANSFER_COMPLETE = 19,

  GAME_READY = 40,
  GAME_DATA,
  // only accept if the progress is indeed further
  GAME_ACTION,
  GAME_ACTION_RESPONSE,
  GAME_CUSTOM_MESSAGE_1,
  GAME_CUSTOM_MESSAGE_2,
  /*
  GAME_CUSTOM_MESSAGE_3,
  GAME_CUSTOM_MESSAGE_4,
  GAME_CUSTOM_MESSAGE_5,
  */
  REVEAL_SECRET,
  GAME_OVER,
}

export const messageSchemas: { [key in PeerMessageType]: Schema } = {
  [PeerMessageType.INITIATE_DATA_TRANSFER]: {},
  [PeerMessageType.NEW_SALE]: {
    expiry: {
      type: "number",
      byteLength: 2,
    },
    timestamp: {
      type: "number",
      byteLength: 8,
    },
    r: {
      type: "number",
      byteLength: 2,
    },
    assets: {
      type: "bignumber",
      byteLength: 20,
    },
    price: {
      type: "bignumber",
      byteLength: 32,
    },
    signature: {
      type: "buffer",
      byteLength: constants.SIGNATURE_LENGTH,
    },
    address: {
      type: "hexstring",
      byteLength: constants.ADDRESS_LENGTH,
    },
    isVerified: {
      type: 'boolean',
    }
  },
  [PeerMessageType.NEW_ROUTER]: {
    address: {
      type: "hexstring",
      byteLength: constants.ADDRESS_LENGTH,
    },
    url: {
      type: "string",
    },
  },
  [PeerMessageType.DATA_TRANSFER_COMPLETE]: {},
  [PeerMessageType.GAME_READY]: {},
  [PeerMessageType.GAME_DATA]: {
    signature: {
      type: "buffer",
      byteLength: constants.SIGNATURE_LENGTH,
    },
    gameData: {
      type: "buffer",
    },
  },
  [PeerMessageType.GAME_ACTION]: {
    actionId: {
      type: "number",
      byteLength: 3,
    },
    signature: {
      type: "buffer",
      byteLength: constants.SIGNATURE_LENGTH,
    },
    // TBD can be extended and further parsed by game
    actionData: {
      type: "buffer",
    },
  },
  [PeerMessageType.GAME_ACTION_RESPONSE]: {
    actionId: {
      type: "number",
      byteLength: 3,
    },
    response: {
      type: "boolean",
    },
  },
  [PeerMessageType.GAME_CUSTOM_MESSAGE_1]: {
    // TBD by specific game
    data: {
      type: "buffer",
    },
  },
  [PeerMessageType.GAME_CUSTOM_MESSAGE_2]: {
    // TBD by specific game
    data: {
      type: "buffer",
    },
  },
  [PeerMessageType.GAME_OVER]: {
    signature: {
      type: "buffer",
      byteLength: constants.SIGNATURE_LENGTH,
    },
  },
  [PeerMessageType.REVEAL_SECRET]: {
    proof: {
      type: "buffer",
    },
  },
};

export type MessageSchemas = {
  [PeerMessageType.INITIATE_DATA_TRANSFER]: AppliedSchema;
  [PeerMessageType.NEW_SALE]: AppliedSchema & SaleDetails;
  [PeerMessageType.NEW_ROUTER]: AppliedSchema & Router;
  [PeerMessageType.DATA_TRANSFER_COMPLETE]: AppliedSchema;

  [PeerMessageType.GAME_READY]: AppliedSchema;
  [PeerMessageType.GAME_DATA]: GameReadyWithDataSchema;
  // only accept if the progress is indeed further
  [PeerMessageType.GAME_ACTION]: ActionSchema;
  [PeerMessageType.GAME_ACTION_RESPONSE]: ActionResponseSchema;
  [PeerMessageType.GAME_CUSTOM_MESSAGE_1]: CustomMessageSchema;
  [PeerMessageType.GAME_CUSTOM_MESSAGE_2]: CustomMessageSchema;
  [PeerMessageType.GAME_OVER]: GameOverSchema;
  [PeerMessageType.REVEAL_SECRET]: RevealSecretSchema;
};

// don't actually need to export, just the above
export type ActionSchema = AppliedSchema & {
  actionId: number;
  signature: Uint8Array;
  actionData: Uint8Array;
};

export type ActionResponseSchema = AppliedSchema & {
  actionId: number;
  response: boolean;
};

export type CustomMessageSchema = AppliedSchema & {
  data: Uint8Array;
};

export type GameOverSchema = AppliedSchema & {
  signature: Uint8Array;
};

export type RevealSecretSchema = AppliedSchema & {
  proof: Uint8Array;
};

export type GameReadyWithDataSchema = AppliedSchema & {
  signature: Uint8Array;
  gameData: Uint8Array;
};
