// holds connections and facilitates authentication
// also tests routers at random intervals for their app responses
// a pseudomodule that lives alongside RzRSockets
import { ConnectionStatus } from "../types";
import RzRDB from "../RzRDB";
import Observable from "../Observable";
import Ethereum from "../Ethereum";
import {
  once,
  buildSchema,
  applySchema,
  compressSale,
  watch,
  timer,
} from "../utils";
import { BigNumber, utils, Wallet } from "ethers";
import logger from "../Log";

import {
  PingSchema,
  SignalMessageType,
  messageSchemas,
  MessageSchemas,
  SignalMessageSchema,
  OfferSchema,
  AnswerSchema,
  CandidateSchema,
} from "./messageTypes";

const log = (s: string, slot?: number) => logger.log(s, "Router", slot);
const _log = logger._log("Router");

type RouterProtocol = WebSocket;
// ^^ this is to be generalized

export type RouterConnectionEventMap = MessageSchemas & {
  socketConnectionStatusChanged: ConnectionStatus;
  authMessageRecieved: Uint8Array;
};

export default class RouterConnection extends Observable<RouterConnectionEventMap> {
  socket: RouterProtocol;
  status: ConnectionStatus = {
    isConnected: false,
    isAuthorized: false,
    isAuthorizing: false,
  };
  modules: {
    ethereum: Ethereum;
    rzrDB: RzRDB;
  };

  constructor(rzrDB: RzRDB, ethereum: Ethereum, url: string) {
    super();

    this.modules = {
      rzrDB,
      ethereum,
    };

    this.socket = new WebSocket(url);

    this.socket.binaryType = "arraybuffer";

    this.socket.onopen = this.onConnectionOpen;
    this.socket.onclose = this.onConnectionClose;

    this.socket.addEventListener("message", this.listen);
  }

  reset() {
    this.socket.close();
    this.removeAllListeners();
  }

  private listen = ({ data }: { data: ArrayBuffer }) => {
    const dataArray = new Uint8Array(data);

    if (this.status.isAuthorizing) {
      this.status.isAuthorizing = false;
      if (dataArray[0] === 0) throw new Error("authentication issue");
      this.status.isAuthorized = true;
      this.emit("socketConnectionStatusChanged", this.status);
      log("connected");
      return;
    } else if (!this.status.isAuthorized) {
      this.emit("authMessageRecieved", dataArray);
      this.status.isAuthorizing = true;
      return;
    }

    const messageType: SignalMessageType = dataArray[0];

    const set = this.listeners[messageType];

    if (!set) return;

    set.forEach((listener) =>
      listener(applySchema(messageSchemas[messageType], dataArray.slice(1)))
    );
  };

  private onConnectionOpen = async () => {
    this.status.isConnected = true;

    const identity = this.modules.ethereum.identity;

    if (identity === null) throw new Error("Identity not found");

    this.socket.send(utils.arrayify(identity.address));

    const authMessage = await once(this, "authMessageRecieved");

    this.socket.send(utils.arrayify(await identity.signMessage(authMessage)));
  };

  private onConnectionClose = () => {
    this.status = {
      isConnected: false,
      isAuthorized: false,
      isAuthorizing: false,
    };
  };

  send<K extends keyof MessageSchemas>(
    messageType: K,
    data: MessageSchemas[K]
  ) {
    return this.socket.send(
      utils.concat([
        new Uint8Array([messageType]),
        new Uint8Array(buildSchema(messageSchemas[messageType], data)),
      ])
    );
  }
}
