import { Wallet, utils, providers } from "ethers";

import EthereumProvider from "./provider";
import { SignatureDetails } from "../../types";

import { watch } from "../../utils";

import logger from "../../Log";
const _log = logger._log("Provider");

const localProvider = {
  getBlock: () => Promise.resolve({number:1}),
  isLocal: true
};

export default class LocalProvider extends EthereumProvider {
  provider = localProvider;

  initialize() {
    this.signer = new Wallet(utils.randomBytes(32));

    this.emit("accountChanged", {
      signer: this.signer,
      provider: this.provider,
    });

    this.emit("connectionStatusChanged", true)
  }

  async sign(
    signatureData: Uint8Array,
  ): Promise<string> {
    if (!this.signer) throw new Error('no signer')
    return this.signer.signMessage(signatureData);
  }
}
