import { useCallback, useState, useRef, useEffect } from "react";
import { useParams, useNavigate } from "react-router";
import { BigNumber, utils, Wallet } from "ethers";
import { timer } from "../../utils";
import rzr, { RzRShards, RzRShardState } from "../../rzr";

const useGame = () => {
  const { token = "" } = useParams();
  const navigate = useNavigate();
  const [actions, setActions] = useState<{
    endTurn: (stand: boolean) => void;
    endGame: () => void;
    playAsset: (index: number, replace: boolean) => void;
  } | null>(null);
  const [isOpponentConnected, setConnectionStatus] = useState<boolean | null>(
    null
  );
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [gameState, setGameState] = useState<RzRShardState>({
    opponentUsedAssetCount: 0,
    availableAssets: [],
    sequences: [[], []],
    sequencePosition: 0,
    gameProgress: 0,
    boards: [[], []],
    boardSizes: [0, 0],
    priority: 0,
    secretsDeclared: [false, false],
    assetsChosen: [false, false],
    isStanding: [false, false],
    hasRevealed: [false, false],
    myIndex: 0,
    setResults: [0, 0],
    isLastElementReplacable: false,
  });

  useEffect(() => {
    let rzrShards: null | RzRShards = null;
    const onGameOver = () => {
      return navigate("/play");
    };
    rzr.loadGame(token).then((_rzrShards) => {
      rzrShards = _rzrShards;

      setActions({
        endTurn: _rzrShards.endTurn,
        endGame: () => _rzrShards.endGame() /*.then(() => navigate("/play"))*/,
        playAsset: _rzrShards.playAsset,
      });

      rzrShards.addEventListener(
        "opponentConnectionStatusChanged",
        setConnectionStatus
      );
      rzrShards.addEventListener("gameChanged", setGameState);
      rzrShards.addEventListener("gameOver", onGameOver);
    });

    return () => {
      if (rzrShards) {
        rzrShards.removeEventListener(
          "opponentConnectionStatusChanged",
          setConnectionStatus
        );
        rzrShards.removeEventListener("gameChanged", setGameState);
        rzrShards.removeEventListener("gameOver", onGameOver);
      }
      rzr.closeGame(token);
    };
  }, [token, navigate]);
  /*

  useEffect(() => {
    // can't let players play more than a game at a time
    if (rzrShards.token && token !== rzrShards.token)
      return navigate("/lounge");

    const onGameOver = () => navigate("/play");
    rzrShards.addEventListener("loadingStatusChanged", setIsLoading);
    rzrShards.addEventListener("gameOver", onGameOver);
    rzrShards.addEventListener("gameChanged", setGameState);
    rzrShards.addEventListener(
      "opponentConnectionStatusChanged",
      setConnectionStatus
    );

    rzrShards.build(token);

    return () => {
      rzrShards.removeEventListener("loadingStatusChanged", setIsLoading);
      rzrShards.removeEventListener("gameOver", onGameOver);
      rzrShards.removeEventListener("gameChanged", setGameState);
      rzrShards.removeEventListener(
        "opponentConnectionStatusChanged",
        setConnectionStatus
      );
    };
  }, [token, navigate]);*/

  return {
    isLoading,
    // opponent: rzrShards.opponent.address,
    // player: rzrShards.player,
    isOpponentConnected,
    token,
    gameState,
    actions,
  };
};

export default useGame;
