import { Schema, AppliedSchema } from "../types";
import constants from "../constants";

export enum SignalMessageType {
  /*
  CLIENT_DISCONNECT = 2,
  NEW_CLIENT,
  CLIENTS,
  */
  PING = 0,
  CLIENT_UNAVAILABLE,
  // CHANNELS,
  OFFER,
  ANSWER,
  CANDIDATE,
}

export const messageSchemas: { [key in SignalMessageType]: Schema } = {
  // schemas should be arrays..
  // objects follow insertion order for strings, so it works but it's not the most future proof solution...
  [SignalMessageType.PING]: {
    identity: {
      type: "hexstring",
      byteLength: constants.ADDRESS_LENGTH
    },
    encodedSupportedProtocols: {
      type: "number",
      byteLength: 1,
    },
    encodedIntent: {
      type: "number",
      byteLength: 1,
    },
    token: {
      type: "buffer",
      byteLength: 24,
    },
  },
  [SignalMessageType.CLIENT_UNAVAILABLE]: {
    identity: {
      type: "hexstring",
      byteLength: constants.ADDRESS_LENGTH,
    },
  },
  [SignalMessageType.OFFER]: {
    identity: {
      type: "hexstring",
      byteLength: constants.ADDRESS_LENGTH,
    },
    protocolId: {
      type: "number",
      byteLength: 1,
    },
    signature: {
      type: "hexstring",
      byteLength: constants.SIGNATURE_LENGTH,
    },
    requestedIdentity: {
      type: "hexstring",
      byteLength: constants.ADDRESS_LENGTH,
    },
    offerData: {
      type: "buffer",
    },
  },
  [SignalMessageType.ANSWER]: {
    identity: {
      type: "hexstring",
      byteLength: constants.ADDRESS_LENGTH,
    },
    signature: {
      type: "hexstring",
      byteLength: constants.SIGNATURE_LENGTH,
    },
    answer: {
      type: "buffer",
    },
  },
  [SignalMessageType.CANDIDATE]: {
    identity: {
      type: "hexstring",
      byteLength: constants.ADDRESS_LENGTH,
    },
    encodedCandidate: {
      type: "buffer",
    },
  },
};

export type SignalMessageSchema = AppliedSchema & {
  identity: string;
};

export type MessageSchemas = {
  [SignalMessageType.PING]: PingSchema;
  [SignalMessageType.CLIENT_UNAVAILABLE]: SignalMessageSchema;
  [SignalMessageType.OFFER]: OfferSchema;
  [SignalMessageType.ANSWER]: AnswerSchema;
  [SignalMessageType.CANDIDATE]: CandidateSchema;
};

export type PingSchema =  SignalMessageSchema & {
  encodedIntent: number;
  encodedSupportedProtocols: number;
  token: Uint8Array;
};

export type OfferSchema = SignalMessageSchema & {
  protocolId: number;
  signature: string;
  offerData: Uint8Array;
};

export type AnswerSchema = SignalMessageSchema & {
  signature: string;
  answer: Uint8Array;
};

export type CandidateSchema = SignalMessageSchema & {
  encodedCandidate: Uint8Array;
};
