import { useState } from 'react'
const useSet = <T>(initialValue: T[] = []) => {
  const [list, setList] = useState<T[]>(initialValue)

  const add = (element: T) => {
    setList([...list, element])
  }

  const remove = (element: T) => {
    setList(list.filter(value => value !== element))
  }

  const removeAt = (index: number) => {
    setList([...list.slice(0, index - 1), ...list.slice(index)])
  }

  return {
    list,
    add,
    remove,
    removeAt,
    set: setList
  }
}

export default useSet
