import { useContext, useState, useRef } from "react";
import { useNavigate } from "react-router";
import { Input } from "../../lvl1/Form";
import SaleTemplate from "./SaleTemplate";
import { ActionButton } from "../../lvl1/Button";
import styled from "styled-components";
import RzR from "../../../contexts/RzR";
import BN from "bn.js";
import { utils } from "ethers";
import Modal, { CloseIcon } from "../../lvl1/Modal";
import { Inline } from "../../lvl1/Primitives";
import AssetSelector from "../AssetSelector";
import { H3 } from "../../lvl1/Typography";
import { getShardAsAsset, digestSale } from "../../../utils";
import rzr, { SHARDS, Shard }  from "../../../rzr";

const Label = styled.label`
  font-size: 0.875rem;
`;

const assetsToSingularShards = (assets: number[]): Shard[] =>
  assets.reduce(
    (shards, assetCount, assetLvl) =>
      assetCount === 0 ? shards : [...shards, SHARDS[assetLvl]],
    [] as Shard[]
  );

type FormElement = HTMLFormElement & {
  elements: {
    price: HTMLInputElement;
    expiry: HTMLInputElement;
    amount: HTMLInputElement;
  };
};

const NewSaleC = () => {
  const {
    account: { assets },
  } = useContext(RzR);
  const [isModalOpen, setIsModalOpen] = useState<boolean>(false);
  const [selectedAsset, setSelectedAsset] = useState<Shard | null>(null);
  const navigate = useNavigate();

  const onSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const { price, expiry, amount } = (e.target as FormElement).elements;
    const match = expiry.value
      .replaceAll(" ", "")
      .match(/(\d+)d|\s+|(\d+)h|\s+|(\d+)m/gi);

    if (!match || !selectedAsset) return;

    const expiryValue = match.reduce((sum, time) => {
      const value = parseInt(time.slice(0, -1));
      const t = time.slice(-1);
      return sum + (t === "d" ? value * 1440 : t === "h" ? value * 60 : value);
    }, 0);

    rzr
      .createSale({
        expiry: expiryValue,
        assets: getShardAsAsset(selectedAsset).mul(amount.value),
        price: utils.parseUnits(price.value, "ether"),
      })
      .then(() => navigate("/market"));
  };

  if (isModalOpen) {
    return (
      <Modal>
        <Inline $justify="space-between" $align="center" $margin="md">
          <H3 $align="center">
            select the shard you are ready to say goodbye to
          </H3>
          <CloseIcon onClick={() => setIsModalOpen(false)} />
        </Inline>
        <AssetSelector
          assets={assetsToSingularShards(assets)}
          onSelect={(i, shard) => {
            setSelectedAsset(shard);
            setIsModalOpen(false);
          }}
        />
      </Modal>
    );
  }

  return (
    <form onSubmit={onSubmit}>
      <SaleTemplate
        onAssetClick={() => setIsModalOpen(true)}
        asset={selectedAsset || {}}
        price={<Input name="price" />}
        amount={<Input defaultValue="1" name="amount" />}
        context={{
          title: "Signatures",
          content: <p>4 (1, 3, 5, 10)</p>,
        }}
        expiry={<Input name="expiry" defaultValue="1d 0h 0m" />}
        action={<ActionButton $primary>Sell</ActionButton>}
      />
    </form>
  );
};

export default NewSaleC;

