import { FC } from "react";
import styled, { css } from "styled-components";

export const ProgressContainer = styled.div<IProgressBar>`
  height: 100%;
  width: 0.25rem;
  position: relative;
  background: white;
  border-radius: 5px;

  ::before {
    position: absolute;
    right: 0.75rem;
    top: 0;
    content: "${({ min }) => min || 0}";
    color: white;
    height: 1rem;
  }

  ::after {
    position: absolute;
    right: 0.75rem;
    bottom: 0;
    content: "${({ max }) => max || 0}";
    color: white;
    height: 1rem;
  }
`;

const Progress = styled.progress<IProgressBar>`
  width: 100%;
  opacity: 0;
`;
interface IVisibleProgress {
  progress: number; // normalized to 0-100
}
const VisibleProgress = styled.div<IVisibleProgress>`
  border-radius: 5px;
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  height: 100%;
  content: " ";
  height: ${({ progress }) => progress}%;
  background: #22acde;
  transition: 0.25s height;
  ${({ progress }) =>
    progress > 0 && progress < 100
      ? css`
          ::after {
            position: absolute;
            content: " ";
            right: 0;
            bottom: 0;
            width: 1rem;
            height: 0.25rem;
            background: #22acde;
          }
        `
      : ``}
`;

interface IProgressBar {
  min?: number;
  max: number;
  value: number;
  asPercent?: boolean;
}
const ProgressBar: FC<IProgressBar> = ({ min, max, value, asPercent }) => {
  const clampedValue = value > max ? max : value;

  return (
    <ProgressContainer min={min} max={asPercent ? 100 : max} value={clampedValue}>
      <Progress max={max} value={clampedValue} />
      <VisibleProgress progress={(clampedValue / max)  * 100} />
    </ProgressContainer>
  );
};

export default ProgressBar;
