import { FC } from "react";
import styled, { css, keyframes } from "styled-components";
const highlight = keyframes`
to {
    background: rgba(255, 255, 255, 0.25);
}
from {
    background: rgba(255, 255, 255, 0.5);
}
`

const upTop = keyframes`
0% {
  transform: translateY(-80%);
}
50% {
  transform: translateY(-81%);
}
100% {
  transform: translateY(-80%);
}
`;
const downBottom = keyframes`
0% {
  transform: translateY(45%);
}
50% {
  transform: translateY(46%);
}
100% {
  transform: translateY(45%);
}
`;
const rightSide = keyframes`
0% {
  transform: translateX(0%);
}
50% {
  transform: translateX(1%);
}
100% {
  transform: translateX(0%);
}
`;
const leftSide = keyframes`
0% {
  transform: translateX(0%);
}
50% {
  transform: translateX(-1%);
}
100% {
  transform: translateX(0%);
}
`;

const commonStyles = css`
  display: flex;
  width: 100%;
  height: 100%;
  position: absolute;
  align-items: center;
  justify-content: center;
`;

type Part = {
  color: "red" | "blue" | "alternate" | "gray" | "lightgray";
};

type CenterPart = Part & {
  hasOptionalValue?: boolean;
};

const Center = styled.div<CenterPart>`
  ${commonStyles}

  background: var(--neutral-color);
  font-family: "Wallpoet", cursive;
  clip-path: polygon(50% 20%, 75% 35%, 75% 65%, 50% 80%, 25% 65%, 25% 35%);
  /*transform: scale(0.85);
  background: ${({ color }) =>
    color === "lightgray"
      ? "var(--neutral-asset-color)"
      : color === "gray"
      ? "var(--neutral-color)"
      : color === "red"
      ? "var(--red-color)"
      : "#26619c"};
  * */
  font-size: ${({ hasOptionalValue }) =>
    hasOptionalValue ? `1.25em` : `1.75em`};
  transition: 0.25s transform;
  ${({ hasOptionalValue }) =>
    hasOptionalValue
      ? css`
          span:first-of-type {
            position: relative;
            top: -0.25em;
          }
          span:last-of-type {
            position: relative;
            bottom: -0.25em;
          }
        `
      : ``}
`;

const Sides = styled.div<Part>`
  ${commonStyles}
  ::before,
  ::after {
    position: absolute;
    width: 100%;
    height: 100%;
    content: " ";
    transition: background 0.25s;
  }
  ::before {
    background: ${({ color }) =>
      color === "lightgray"
        ? "var(--neutral-asset-color)"
        : color === "gray"
        ? "var(--neutral-color)"
        : color === "red"
        ? "var(--red-color)"
        : "var(--blue-color)"};
    left: -3px;
    clip-path: polygon(
      0% 50%,
      25% 5%,
      50% 20%,
      25% 35%,
      25% 65%,
      50% 80%,
      25% 95%
    );
  }
  ::after {
    right: -3px;
    background: ${({ color }) =>
      color === "lightgray"
        ? "var(--neutral-asset-color)"
        : color === "gray"
        ? "var(--neutral-color)"
        : color === "blue"
        ? "var(--blue-color)"
        : "var(--red-color)"};
    clip-path: polygon(
      100% 50%,
      75% 5%,
      50% 20%,
      75% 35%,
      75% 65%,
      50% 80%,
      75% 95%
    );
  }
`;

const Top = styled.div<Part>`
  ${commonStyles}
  top: -2px;
  background: var(--neutral-color);
  transform: translateY(-80%);
  clip-path: polygon(75% 85%, 50% 100%, 25% 85%, 50% 35%);

  ::before,
  ::after {
    position: absolute;
    content: " ";
    width: 10%;
    height: 10%;
    ${({ color }) =>
      color === "gray"
        ? ""
        : color === "blue"
        ? css`
            border-top: 3px solid var(--blue-color);
            border-left: 3px solid var(--blue-color);
          `
        : css`
            border-bottom: 3px solid var(--red-color);
            border-right: 3px solid var(--red-color);
          `}
  }
  ::before {
    transform: translateY(
        ${({ color }) => (color === "blue" ? "125%" : "100%")}
      )
      rotate(45deg);
  }
  ::after {
    transform: translateY(
        ${({ color }) => (color === "blue" ? "225%" : "200%")}
      )
      rotate(45deg);
  }
`;
const Bottom = styled.div<Part>`
  ${commonStyles}
  bottom: -2px;
  background: var(--neutral-color);
  transform: translateY(45%);
  clip-path: polygon(75% 50%, 50% 100%, 25% 50%, 50% 35%);
  ::before,
  ::after {
    position: absolute;
    content: " ";
    width: 10%;
    height: 10%;
    ${({ color }) =>
      color === "gray"
        ? ""
        : color === "blue"
        ? css`
            border-top: 3px solid var(--blue-color);
            border-left: 3px solid var(--blue-color);
          `
        : css`
            border-bottom: 3px solid var(--red-color);
            border-right: 3px solid var(--red-color);
          `}
  }
  ::before {
    transform: translateY(${({ color }) => (color === "blue" ? "50%" : "25%")})
      rotate(45deg);
  }
  ::after {
    transform: translateY(
        ${({ color }) => (color === "blue" ? "150%" : "125%")}
      )
      rotate(45deg);
  }
`;

type IAsset = {
  sign?: number;
  value?: number;
  optionalValue?: number;
};

const Asset: FC<IAsset> = ({ sign, value, optionalValue }) => {
  return (
    <>
      <Top
        color={
            sign === undefined
            ? "gray"
            : [0, 2].includes(sign)
            ? "blue"
            : "red"
        }
      />
      <Sides
        color={
          value === undefined
            ? "lightgray"
            : sign === undefined
            ? "gray"
            : sign === 0
            ? "blue"
            : sign === 1
            ? "red"
            : "alternate"
        }
      />
      <Center
        color={
          value === undefined
            ? "lightgray"
            : sign === undefined
            ? "gray"
            : sign === 0
            ? "blue"
            : sign === 1
            ? "red"
            : "alternate"
        }
        hasOptionalValue={optionalValue !== undefined}
      >
        {value ? (
          optionalValue ? (
            <>
              <span>{value}</span>/<span>{optionalValue}</span>
            </>
          ) : (
            value
          )
        ) : (
          "?"
        )}
      </Center>
      <Bottom
        color={
          sign === undefined ? "gray" : [1, 2].includes(sign) ? "red" : "blue"
        }
      />
    </>
  );
};

export default Asset;
export const styles = css`
  color: white;
  clip-path: none;
  transform: rotate(0deg) translateY(50%) scale(0.8, 0.9) !important;
  background: none !important;
  z-index: 0;

  --red-color: #9b111e;
  --blue-color: #26619c;
  --backdrop-color: #ddd;
  --hollow-color: rgba(75, 75, 75, 0.25);
  --active-hollow-color: rgba(150, 50, 50, 0.5);
  --neutral-color: #111;
  --neutral-asset-color: #2a2a6a;

  ::after {
    box-shadow: 0 0 0.5em var(--backdrop-color);
    content: " ";
    width: 100%;
    height: 100%;
    position: absolute;
    transform: scale(1.65, 1.085) rotate(67.5deg) skewX(45deg)
      scaleY(0.70710678118) scaleX(0.86602540378);
    border-radius: 2px;

    background: rgba(255, 255, 255, 0.5);
    z-index: -1;
    transition: 0.25s background, 0.25s box-shadow;
    box-sizing: border-box;
  }
  :hover {
    /*
    ${Center} {
      transform: scale(1) rotate(360deg);
    }
    ${Top} {
      animation: ${upTop} 0.5s infinite;
    }
    ${Bottom} {
      animation: ${downBottom} 0.5s infinite;
    }
    ${Sides} {
      ::after {
        animation: ${rightSide} 0.5s infinite;
      }
      ::before {
        animation: ${leftSide} 0.5s infinite;
      }
    }
    */
    &::after {
      box-shadow: 0px 0px 1em var(--backdrop-color);
      background: rgba(255, 255, 255, 0.75);
    }
  }
`;
export const selectedStyles = css`
  ${styles}
  ::after {
    z-index: 1;
  }
  `;

export const highlightedStyles = css`
  ${styles}
  ::after {
    z-index: 1;
    animation: ${highlight} .5s alternate infinite;
  }
  :hover::after {
    animation: none;
  }
`;

export const disabledStyles = css`
  ${selectedStyles}
  pointer-events: none;
  ::after {
    box-shadow: 0 0 1em var(--backdrop-color);
  }
  :hover {
    cursor: not-allowed;
  }
`;

export const hollowStyles = css`
  ${styles}
  pointer-events: none;
  ::after {
    background: var(--hollow-color);
    box-shadow: 0 0 1.5em var(--hollow-color);
  }
`;

export const activeHollowStyles = css`
  ${hollowStyles}
  pointer-events: none;
  ::after {
    background: var(--active-hollow-color);
    box-shadow: 0 0 1.5em var(--active-hollow-color);
  }
`;
