import { FC, useContext, useState, useEffect } from "react";
import { numberToTimeString } from "../../../utils";
import SaleTemplate from "./SaleTemplate";
import { ActionButton } from "../../lvl1/Button";
import { formatAddress } from "../../../utils";
import { BigNumber, utils, constants } from "ethers";
import rzr, { SHARDS, SaleDetails } from '../../../rzr'

const assetToShard = (
  asset: BigNumber
): {
  sign: number;
  value: number;
} => {
  let i = 0;
  for (i; asset.toString() !== "0"; ++i) {
    asset = asset.shr(8);
  }
  return SHARDS[i - 1];
};

const Sale: FC<{ sale: SaleDetails }> = ({ sale }) => {
  const expiresAt =
    new Date(sale.timestamp * 1000).getTime() + sale.expiry * 60 * 1000;

  const [timeDiff, setTimeDiff] = useState<number>(expiresAt - Date.now());

  useEffect(() => {
    if (timeDiff < 0) return;
    const interval = setTimeout(
      () => setTimeDiff(expiresAt - Date.now()),
      timeDiff > 60 * 60 * 1000 ? 60000 : 1000
    );
    return () => {
      clearTimeout(interval);
    };
  }, [expiresAt, timeDiff]);

  // create board in the background, it moves as you select new one
  // think if asset contract is feasible
  // current standpoint, mint would be an issue
  // reward equals 2nd played shard, it is random in the beginning, must check validity, can do includesOne function (& 0x33)

  return (
    <SaleTemplate
      asset={assetToShard(sale.assets)}
      amount={3}
      price={utils.formatEther(sale.price.toString())}
      context={{ title: "seller", content: formatAddress(sale.address) }}
      expiry={numberToTimeString(timeDiff)}
      action={
        <ActionButton onClick={() => rzr.buy(sale)}>Buy</ActionButton>
      }
    />
  );
};

export default Sale;
