import { utils } from "ethers";

import RzRSocket, {
  SignalMessageType,
  OfferSchema,
  SignalMessageSchema,
  RzRSocketEventMap,
  OperationModes,
} from "../RzRSocket";
import Ethereum from "../Ethereum";
import RzRDB from "../RzRDB";
import constants from "../constants";
import Observable, { Listener } from "../Observable";
import logger from "../Log";
import {
  watch,
  compressSale,
  timer,
  bytesToNumber,
  buildSchema,
  applySchema,
  once,
} from "../utils";
import Comm from "./protocols/comm";

import {
  PeerMessageType,
  messageSchemas,
  MessageSchemas,
} from "./messageTypes";

import ConnectionMatrix from "../ConnectionMatrix";

export { PeerMessageType };

// ONLY ONE PEER WITH INTENT GAME IS ALLOWED
//
// matrix view of all data received from peers and sockets
// actually I could simulate rzr sockets by running several routers on local machine with different paths

export * from "./messageTypes";

const log = (s: string, slot?: number) => logger.log(s, "RzRPeer", slot);
const _log = logger._log("RzRPeer");

const TOKEN_BYTE_LENGTH = 24;

export type CommunicationProtocols = {
  WebRTC: null | typeof Comm;
  WebSocket: typeof Comm;
};

let supportedProtocols: CommunicationProtocols = {
  WebRTC: null,
  WebSocket: Comm,
};

export enum IntentType {
  SYNC_UPSTREAM,
  SYNC_DOWNSTREAM,
  PLAY_GAME,
}

export enum IntentStatus {
  REQUESTED = 1,
  IN_PROGRESS,
  COMPLETED,
}

export const decodeIntentRequest = (encodedIntent: number): IntentType[] => {
  const intents = [];
  if ((encodedIntent & 0b1) > 0) {
    intents.push(IntentType.SYNC_UPSTREAM);
  }
  if ((encodedIntent & 0b10) > 0) {
    intents.push(IntentType.SYNC_DOWNSTREAM);
  }
  if ((encodedIntent & 0b100) > 0) {
    intents.push(IntentType.PLAY_GAME);
  }

  return intents;
};

type PeerIntents = Map<IntentType, IntentStatus>;

export const encodeIntents = (intent: PeerIntents): number => {
  let encodedIntent = 0;

  // UP vs DOWN flipped to reflect direction
  if (intent.get(IntentType.SYNC_DOWNSTREAM) !== undefined) {
    encodedIntent += 0b1;
  }
  if (intent.get(IntentType.SYNC_UPSTREAM) !== undefined) {
    encodedIntent += 0b10;
  }
  if (intent.get(IntentType.PLAY_GAME) !== undefined) {
    encodedIntent += 0b100;
  }

  return encodedIntent;
};

export const setCommunicationProtocols = (
  _supportedProtocols: CommunicationProtocols
) => {
  supportedProtocols.WebRTC = _supportedProtocols.WebRTC;
  supportedProtocols.WebSocket = _supportedProtocols.WebSocket;
};

// consider new event for specifically disconnects
// and have reason as parameter
// -> size limit reached (careful when playing game)
// -> intents fulfiled
// -> connection closed
type RzRPeerEventMap = MessageSchemas & {
  connectionStatusChanged: boolean;
  intentsFulfilled: boolean;
  /*
  [PeerMessageType.GAME_CUSTOM_MESSAGE_2]: Uint8Array;
  [PeerMessageType.GAME_CUSTOM_MESSAGE_3]: Uint8Array;
  [PeerMessageType.GAME_CUSTOM_MESSAGE_4]: Uint8Array;
  [PeerMessageType.GAME_CUSTOM_MESSAGE_5]: Uint8Array;
  */
};

// any peer is ready for data transfer
export default class RzRPeer extends Observable<RzRPeerEventMap> {
  transferredBytes = 0;
  receivedBytes = 0;
  connectionMatrix: ConnectionMatrix;
  identity: string;
  _protocol: Comm | null = null;
  modules: {
    rzrSocket: RzRSocket | null;
    ethereum: Ethereum;
    rzrDB: RzRDB;
  };

  intents: PeerIntents = new Map();

  token = new Uint8Array();

  get isConnected() {
    if (!this._protocol) return false;
    return this.protocol.isConnected;
  }

  get protocol(): Comm {
    if (!this._protocol) throw new Error("protocol not available yet");
    return this._protocol;
  }

  constructor(
    ethereum: Ethereum,
    rzrDB: RzRDB,
    connectionMatrix: ConnectionMatrix,
    identity: string,
    intents: IntentType[]
  ) {
    super();

    this.identity = identity;

    this.addIntents(intents);

    this.connectionMatrix = connectionMatrix;

    this.modules = {
      rzrSocket: null,
      ethereum: ethereum,
      rzrDB: rzrDB,
    };
  }

  private setProtocol(encodedSupportedProtocols: number) {
    if ((encodedSupportedProtocols & 0b10) === 0 || !supportedProtocols.WebRTC)
      return;

    if (!this.modules.rzrSocket || this.identity === null)
      throw new Error("rzr socket connection or identity error");

    this._protocol = new supportedProtocols.WebRTC(
      this.modules.rzrSocket,
      this.modules.ethereum,
      this.identity
    );

    this.protocol.addEventListener("newMessage", this.onMessage);

    this.protocol.addEventListener(
      "connectionStatusChanged",
      (isConnected: boolean) => {
        log(
          `${this.isConnected ? "connected to" : "disconnected from"} ${
            this.identity
          }`
        );

        if (!isConnected) this.token = new Uint8Array();

        this.emit("connectionStatusChanged", isConnected);
      }
    );

    return 0b10;
  }

  private onMessage = (data: ArrayBuffer | string) => {
    // protocol should make sure not to accept too large data
    // also disconnects after 1MB or something
    if (typeof data === "string") return;

    const _data = new Uint8Array(data);

    this.receivedBytes += data.byteLength;

    const messageType: PeerMessageType = _data[0];

    const set = this.listeners[messageType];

    if (!set) return;

    set.forEach((listener) =>
      listener(applySchema(messageSchemas[messageType], _data.slice(1)))
    );
  };

  send<K extends keyof MessageSchemas>(
    messageType: K,
    data?: MessageSchemas[K]
  ) {
    const message = new Uint8Array(
      data ? buildSchema(messageSchemas[messageType], data) : []
    );
    this.transferredBytes += 1 + (message ? message.byteLength : 0);
    return this.protocol.send(
      utils.concat([new Uint8Array([messageType]), message])
    );
  }

  addIntents(intents: IntentType[]) {
    this.intents = new Map(
      ( intents ).map((intent) => [
        intent,
        IntentStatus.REQUESTED || this.intents.get(intent),
      ])
    );
  }

  updateIntent(intent: IntentType, intentStatus: IntentStatus) {
    this.intents.set(intent, intentStatus);

    if (
      Array.from(this.intents.values()).every(
        (status) => status === IntentStatus.COMPLETED
      )
    ) {
      this.emit("intentsFulfilled", true);
    }
  }

  async ping(
    rzrSocket: RzRSocket,
    encodedSupportedProtocols: number,
    token: Uint8Array,

    // ditch this.. clients should know my most recent identity.
    // if they don't just refuse connectino
    requestedIdentityAddress: string
  ) {
    let yieldConnection = false;

    const myNonce = bytesToNumber(this.token.slice(0, 3)) || 0;
    const incomingNonce = bytesToNumber(token.slice(0, 3));

    if (myNonce < incomingNonce) {
      this.token = token;
      yieldConnection = true;
      this.modules.rzrSocket = rzrSocket;
    }

    // currently only support rtc connections
    const selectedProtocol = this.setProtocol(encodedSupportedProtocols);

    this.protocol.token = this.token;

    if (yieldConnection) {
      this.protocol.closeConnection();
      this.protocol.connect();
    }
  }

  reset(removeListeners = true) {
    if (this._protocol) this._protocol.reset();
    if (removeListeners) this.removeAllListeners();
    if (this.modules.rzrSocket) this.modules.rzrSocket.reset()
    // TODO: disconnect from rzr socket
  }

  private precondition = ({ identity }: SignalMessageSchema) =>
    identity === this.identity;

  // look into moving this back to comm
  // and somehow downcast the class
  // not really supported though..
  connect(maxRetries = 1, retries = 0): Promise<boolean> {
    if (this.isConnected) return Promise.resolve(true);
    if (retries === maxRetries) return Promise.resolve(false);
    if (this.token.length > 0) return Promise.resolve(true);

    const token = (this.token = utils.randomBytes(TOKEN_BYTE_LENGTH));

    const onceConfig = {
      precondition: this.precondition,
      timeoutIn: 5000,
      silent: true,
    };

    const rzrSocket = (this.modules.rzrSocket = new RzRSocket(
      this.modules.rzrDB,
      this.modules.ethereum,
      this.connectionMatrix,
      OperationModes.ACTOR
    ));

    return _log(
      `contacting ${this.identity}`,
      rzrSocket
        .connect(this.identity)
        .then(() => {
          rzrSocket.send(SignalMessageType.PING, {
            identity: this.identity,
            encodedSupportedProtocols: supportedProtocols.WebRTC ? 0b11 : 0b01,
            encodedIntent: encodeIntents(this.intents),
            token: this.token,
          });

          return Promise.race([
            once(rzrSocket, SignalMessageType.CLIENT_UNAVAILABLE, onceConfig),
            once(rzrSocket, SignalMessageType.OFFER, onceConfig),
            watch(() =>
              this.token.toString() !== token.toString() ? undefined : null
            ),
          ]);
        })
        .then(
          async (
            result?: OfferSchema | SignalMessageSchema
          ): Promise<boolean> => {
            if (result === undefined) return true;
            if (!result.signature) {
              throw new Error("player unavailable");
            }

            const offer = result as OfferSchema;

            this.setProtocol(offer.protocolId);

            this.protocol.token = this.token;

            this.protocol.attachOffer(offer.signature, offer.offerData);

            return once(this, "connectionStatusChanged");
          }
        ),
      {
        successMessage: "success",
        failMessage: "player unavailable",
      }
    ).catch((e) => {
      this.token = new Uint8Array();
      this.reset(false);
      return timer(retries * 5000).then(() =>
        this.connect(maxRetries, retries + 1)
      );
    });
  }
}
