import RzRPeer, {
  decodeIntentRequest,
  IntentType,
  PeerMessageType,
  setCommunicationProtocols,
  CommunicationProtocols,
} from "./RzRPeer";

import RzRSocket, {
  PingSchema,
  SignalMessageType,
  RouterConnection,
  OperationModes,
} from "./RzRSocket";

import Ethereum from "./Ethereum";
import RzRDB from "./RzRDB";

import { watch, timer, once } from "./utils";

import logger, { LogEventMap } from "./Log";
import Observable from "./Observable";
import { Router, Player } from "./types";

const log = (s: string, slot?: number) => logger.log(s, "RzR", slot);

export type ConnectionMatrixEventTypes = {
  peersChanged: string[];
  rzrSocketConnectionChanged: boolean;
  newPeerConnection: RzRPeer;
};

/*
 * TODO: this entity should manage router connection <> rzr socket dependencies
 * upon rzr socket destruction, it should check whether the router connection
 * is used in another socket, if it isn't router connection can also be closed
 *
 * */
export default class ConnectionMatrix extends Observable<ConnectionMatrixEventTypes> {
  connections = {
    peers: new Map<string, RzRPeer>(),
    routers: new Map<string, RouterConnection>(),
  };

  gameplayPeerAddress: null | string = null;

  rzrSocket: RzRSocket; // <-- this is my primary socket

  modules: {
    rzrDB: RzRDB;
    ethereum: Ethereum;
  };

  constructor(rzrDB: RzRDB, ethereum: Ethereum) {
    super();

    this.rzrSocket = new RzRSocket(
      rzrDB,
      ethereum,
      this,
      OperationModes.LISTENER
    );

    this.modules = {
      rzrDB,
      ethereum,
    };

    ethereum.addEventListener("identityChainLoaded", this.resetAllConnections);

    this.rzrSocket.addEventListener(SignalMessageType.PING, this.onPing);
    this.rzrSocket.addEventListener(
      "socketConnectionStatusChanged",
      ({ isAuthorized }) => {
        this.emit("rzrSocketConnectionChanged", isAuthorized);
      }
    );
  }

  // if I don't support websockets, must let client know through signaling
  // actually offer is already 2nd lvl
  // first is ping
  //
  // so the workflow goes like this
  // ping -> no response -> release
  //      -> response (with offer) -> answer
  // it can happen that there are 2 pings at the same time
  // is it a problem, well no, just need to put nonce on ping
  // TODO: consider lifting the parse library from previous package
  private onPing = async ({
    identity,
    encodedSupportedProtocols,
    encodedIntent,
    token,
  }: PingSchema) => {
    let player: Player | null = await this.modules.rzrDB.loadPlayerByIdentity(
      identity
    );

    if (!player) throw new Error("player could not be identified");

    const peer = this.getOrCreatePeer(
      player.address,
      player.identity,
      decodeIntentRequest(encodedIntent)
    );

    peer.ping(this.rzrSocket, encodedSupportedProtocols, token, identity);
  };

  resetAllConnections = () => {
    Array.from(this.routers.keys()).forEach((address) => {
      this.releaseRouter(address);
    });

    this.rzrSocket.connect();
  };

  get peers() {
    return this.connections.peers;
  }

  get routers() {
    return this.connections.routers;
  }

  getOrCreatePeer(
    address: string,
    identity: string,
    peerIntent: IntentType | IntentType[]
  ): RzRPeer {
    const existingPeer = this.peers.get(address);
    const intents = Array.isArray(peerIntent) ? peerIntent : [peerIntent];

    if (
      intents.includes(IntentType.PLAY_GAME) &&
      address !== this.gameplayPeerAddress
    ) {
      if (this.gameplayPeerAddress === null) {
        this.gameplayPeerAddress = address;
      } else {
        throw new Error("intent cannot be satisfied");
      }
    }

    if (existingPeer) {
      existingPeer.addIntents(intents);
      return existingPeer;
    }

    const peer = new RzRPeer(
      this.modules.ethereum,
      this.modules.rzrDB,
      this,
      identity,
      intents
    );

    this.peers.set(address, peer);

    this.emit("newPeerConnection", peer);

    this.emit("peersChanged", Array.from(this.peers.keys()));

    peer.addEventListener("intentsFulfilled", () =>
      timer(500).then(() => this.releasePeer(identity))
    );

    return peer;
  }

  releasePeer(address: string) {
    const peer = this.peers.get(address);
    if (!peer) return;

    if (this.gameplayPeerAddress === address) {
      this.gameplayPeerAddress = null;
    }

    log(`${address} released`);

    peer.reset();

    this.emit("peersChanged", Array.from(this.peers.keys()));

    this.peers.delete(address);
  }

  releaseRouter(address: string) {
    const router = this.routers.get(address);
    if (!router) return;

    log(`${address} released`);

    router.reset();

    this.routers.delete(address);
  }

  getOrCreateRouterConnection(router: Router) {
    const connection =
      this.routers.get(router.address) || this.createRouterConnection(router);

    if (connection.status.isAuthorized) return Promise.resolve(connection);

    return once(connection, "socketConnectionStatusChanged", {
      precondition: ({ isAuthorized }) => isAuthorized,
    }).then(() => connection);
  }

  private createRouterConnection(router: Router): RouterConnection {
    const connection = new RouterConnection(
      this.modules.rzrDB,
      this.modules.ethereum,
      router.url
    );

    this.routers.set(router.address, connection);

    return connection;
  }
}
