import Observable, { Listener } from "../Observable";
import { SignatureDetails, Player } from "../types";
import { utils, Transaction, providers, Signer, Wallet } from "ethers";
import { timer, watch } from "../utils";
import logger from "../Log";
import RzRDB from "../RzRDB";
import EthereumProvider, { AccountChangeEvent } from "./providers/provider";
const log = (s: string, slot?: number) => logger.log(s, "Ethereum", slot);
const _log = logger._log("Ethereum");

type Provider = any;

export type EthereumEventMap = {
  accountChanged: Signer;
  chainChanged: string;
  newSignature: SignatureDetails | null;
  identityChainLoaded: boolean | null;
};

type Providers = "metamask" | "local";

/*
 * This module should no longer be referred to as "Ethereum"
 * the idea is that a new blockchain is going to be created using identities
 * each identity is revealed after a new one is generated
 * by doing this, we can always safely validate any identity
 * the blockchain is indexed by identities
 * each identity has its own "blockchain"
 * but only the hash is stored for most, the owners store the entire chain whose merkle tree root = identity hash
 * open identity chains can be stored everywhere, but once they are closed (either at the 30 day or when a new one is created) the hash is calculcated and stored
 *
 * baiscally for each game 32 bytes are stored
 * 1M games = 32MB
 *
 * can further simplify as identity chains can be vertically compressed
 *
 * b b b b b b = c
 *
 * c
 * |
 * c
 * |
 * c
 *
 * horizontal and vertical compression
 *
 * it's more like a blockmatrix than a blockchain
 * */
export default class Ethereum extends Observable<EthereumEventMap> {
  address: string = "";
  // check if this is even required, since now all signers will be stored in peers
  identity: Wallet | null = null;
  chainId: string = "";
  pendingSignature: SignatureDetails | null = null;
  backend: EthereumProvider;
  rzrDB: RzRDB;

  get account() {
    return this.backend.signer;
  }

  get provider() {
    return this.backend.provider;
  }

  constructor(rzrDB: RzRDB, EthereumBackend: typeof EthereumProvider) {
    super();
    this.backend = new EthereumBackend();
    this.rzrDB = rzrDB;

    this.attachListeners();
  }

  initialize = () => {
    this.backend.initialize();
  };

  private attachListeners = async () => {
    this.backend.addEventListener("accountChanged", this.onAccountChanged);
    this.backend.addEventListener(
      "connectionStatusChanged",
      this.onConnectionChange
    );

    this.rzrDB.addEventListener("newPlayerStored", (player: Player) => {
      if (player.address === this.address) this.reloadIdentity();
    });
  };

  reloadIdentity = async () => {
    this.identity = null;
    const player = await this.rzrDB.loadPlayer(this.address);

    if (!player) {
      this.emit("identityChainLoaded", null);
      return;
    }

    try {
      this.identity = await this.rzrDB.loadIdentity(
        player.identity,
        this.address
      );

      if (this.identity) {
        // should drop all connections
        this.emit("identityChainLoaded", true);
      } else {
        this.emit("identityChainLoaded", false);
        // identity is out of sync
        // throw new Error('identity load failed');
      }
    } catch (e) {
      this.emit("identityChainLoaded", false);
    }
  };

  private onAccountChanged = async ({
    provider,
    signer,
  }: AccountChangeEvent) => {
    if (this.address) logger.resetLog();
    const address = await signer.getAddress();
    const hasChanged = address.toLowerCase() !== this.address;
    this.address = address.toLowerCase();
    await this.reloadIdentity();
    if (hasChanged) {
      this.emit("accountChanged", signer);
    }
    log("application authorized");
  };

  private onConnectionChange = (isConnected: boolean) => {
    if (isConnected) log("connected to ethereum");
    else log("disconnected from ethereum");
  };

  getBlock(blockNumber: number) {
    return this.provider.getBlock(blockNumber);
  }

  getTransaction(hash: string): Promise<Transaction> {
    return this.provider.getTransaction(hash);
  }

  async requestSignature(
    signatureData: Uint8Array,
    signatureDetails: SignatureDetails
  ): Promise<string> {
    const slot = log("requesting signature...");
    if (this.identity) {
      const signature = await this.identity.signMessage(signatureData);

      log("done", slot);

      return signature;
    }
    try {
      this.emit("newSignature", signatureDetails);
      const signature = await this.backend.sign(signatureData);
      log("done", slot);
      return signature;
    } catch (e) {
      log("failed", slot);
      throw e;
    } finally {
      this.pendingSignature = null;
      this.emit("newSignature", null);
    }
  }
}
