import { useState, useEffect } from "react";
import styled, { css, FlattenSimpleInterpolation } from "styled-components";

import { useParams } from "react-router";
import { BigNumber, utils, Wallet } from "ethers";
import Board, { Element, Position } from "../lvl2/Board";
import Asset, {
  highlightedStyles,
  styles,
  selectedStyles,
  activeHollowStyles,
  hollowStyles,
} from "../lvl1/Asset";
import { actionStyles } from "../lvl1/Triangle";
import { ActionButton } from "../lvl1/Button";
import { H4, H3, H2, P, Label } from "../lvl1/Typography";
import { Stack, Inline } from "../lvl1/Primitives";
import OpponentDisconnectedModal from "../lvl2/OpponentDisconnectedModal";
import { ShardElement, SHARDS } from "../../rzr";
import useGame from "./useGame";

const createElement = (
  p: Position,
  asset?: ShardElement | null,
  action?: () => void
): Element => ({
  styles: action
    ? highlightedStyles
    : asset
    ? styles
    : asset === null
    ? activeHollowStyles
    : hollowStyles,
  position: [p, p, p],
  content: asset ? <Asset sign={asset.sign} value={asset.value} /> : null,
  action,
});

const createAssetElement = (
  p: Position,
  asset?: ShardElement | null,
  action?: () => void,
  isSelected = false
): Element => ({
  styles: isSelected
    ? selectedStyles
    : asset
    ? styles
    : asset === null
    ? activeHollowStyles
    : hollowStyles,
  position: [p, p, p],
  content: asset ? <Asset sign={asset.sign} value={asset.value} /> : null,
  action,
});

