const Log = styled.footer`
  color: white;
  position: fixed;
  right: 3vw;
  top: 2vh;
`;

const CTAContainer = styled.footer`
  color: white;
  position: fixed;
  right: 3vw;
  bottom: 2vh;
`;

const logStyles = css`
  color: white;
`;

const scoreStyles = css`
  ${logStyles}
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
`;

const createContent = (
  p: Position,
  content: React.ReactElement,
  styles?: FlattenSimpleInterpolation
): Element => ({
  styles: styles ? styles : logStyles,
  position: [p, p, p],
  content,
  isFullSize: true,
});

const createAction = (
  p: Position,
  styles?: FlattenSimpleInterpolation,
  content?: React.ReactElement
): Element => ({
  styles,
  isFullSize: true,
  position: [p, p, p],
  content: content || null,
});

const positions = [
  {
    board: [
      [3, 3],
      [2, 2],
      [3, 1],
      [4, 0],
      [5, 1],
      [6, 2],
      [5, 3],
      [4, 2],
    ],
    assets: [
      [1, 3],
      [7, 3],
      [2, 4],
      [6, 4],
    ],
  },
  {
    board: [
      [11, 1],
      [10, 2],
      [11, 3],
      [12, 4],
      [13, 3],
      [14, 2],
      [13, 1],
      [12, 2],
    ],
    assets: [
      [10, 0],
      [9, 1],
      [14, 0],
      [15, 1],
    ],
  },
];

const Game = () => {
  const {
    token,
    isOpponentConnected,
    isLoading,
    // opponent,
    // player,
    gameState,
    actions,
  } = useGame();

  const [selectedAssetIndex, setSelectedAssetIndex] = useState<number | null>(
    null
  );

  if (isLoading || !actions) return <>loading</>;

  const state = {
    boards: [
      gameState.boards[0].map((element, i) => {
        if (i >= gameState.boardSizes[0]) return undefined;
        return gameState.boards[0][i];
      }),
      gameState.boards[1].map((element, i) => {
        if (i >= gameState.boardSizes[1]) return undefined;
        return gameState.boards[1][i];
      }),
    ],
    assets: [
      gameState.availableAssets.map((shardIndex) =>
        shardIndex === null ? null : SHARDS[shardIndex]
      ),
      [
        ...[...new Array(4 - gameState.opponentUsedAssetCount)].map(() => ({})),
        ...[...new Array(gameState.opponentUsedAssetCount)].map(() => null),
      ],
    ],
  };

  const values = [
    state.boards[0].reduce(
      // TODO: 0 check not necessary
      (sum, element) => (element ? (element.value || 0) + sum : sum),
      0
    ),
    state.boards[1].reduce(
      (sum, element) => (element ? (element.value || 0) + sum : sum),
      0
    ),
  ];

  const board = state.boards.flatMap((_board, playerIndex) => {
    if (!gameState.secretsDeclared[playerIndex]) return [];

    const boardIndex = gameState.myIndex === 1 ? playerIndex ^ 1 : playerIndex;

    let displayed = 0;
    const toDisplay =
      selectedAssetIndex !== null
        ? gameState.isLastElementReplacable
          ? 2
          : 1
        : 0;

    return _board.map((element, j) => {
      const isHighlighted =
        playerIndex === gameState.myIndex &&
        (element === undefined ||
          (_board[j + 1] === undefined && gameState.isLastElementReplacable)) &&
        displayed++ < toDisplay;

      return createElement(
        [
          positions[boardIndex].board[j][0] - 0 * 2,
          positions[boardIndex].board[j][1],
        ],
        element,
        isHighlighted
          ? () => {
              actions.playAsset(
                selectedAssetIndex as number,
                element !== undefined
              );
              setSelectedAssetIndex(null);
            }
          : undefined
      );
    });
  });

  const assets = state.assets.flatMap((_assets, i) => {
    if (!gameState.assetsChosen[i]) return [];
    const isOwn = i === 0;
    return _assets.map((element, j) =>
      createAssetElement(
        [positions[i].assets[j][0] - 0 * 2, positions[i].assets[j][1]],
        element,
        gameState.myIndex === gameState.priority && isOwn
          ? () => setSelectedAssetIndex(selectedAssetIndex === j ? null : j)
          : undefined,
        isOwn && selectedAssetIndex === j
      )
    );
  });

  const status = [
    createContent(
      [4, 4],
      <H3 $font="secondary">{values[gameState.myIndex]}</H3>,
      scoreStyles
    ),
    createContent(
      [12, 1],
      <H3 $font="secondary">{values[gameState.myIndex ^ 1]}</H3>,
      scoreStyles
    ),
  ];

  const actionItems = gameState.assetsChosen.every((i) => i)
    ? [
        createAction(
          [4, 5],
          actionV1Styles,
          <Label $align="center" $font="secondary">
            <span>stand</span>
            <button
              onClick={() => {
                setSelectedAssetIndex(null);
                actions.endTurn(true);
              }}
            />
          </Label>
        ),
        createAction(
          [3, 2],
          actionV4Styles,
          <Label $align="center" $font="secondary">
            <button onClick={() => actions.endTurn(false)} />
            <span>end turn</span>
          </Label>
        ),
      ]
    : [];

  return (
    <Stack style={{ paddingTop: "5vw" }}>
      {isOpponentConnected === false && (
        <OpponentDisconnectedModal token={token} />
      )}
      <CTAContainer>
        <ActionButton onClick={actions.endGame}>give up</ActionButton>
      </CTAContainer>
      <Inline $justify="center">
        <Board
          elements={[
            ...(gameState.myIndex === gameState.priority ? actionItems : []),
            ...board,
            ...assets,
            ...status,
          ]}
          responsiveCols={[9, 17, 17]}
          responsiveRows={[6, 6, 6]}
        />
      </Inline>
      <Log>
        <Stack>
          <H4 $font="secondary" $align="right">
            wager
          </H4>
          <P $size="sm" $font="tertiary">
            0.43247
          </P>
          <H4 $font="secondary" $align="right">
            opponent{/*isOpponentConnected ? null : "*"*/}
          </H4>
          <P $size="sm" $font="tertiary">
            0xb562...fa2
          </P>
        </Stack>
      </Log>
    </Stack>
  );
};

export default Game;
