import { useContext, useEffect, useState } from "react";
import styled from "styled-components";
import { useLocation } from "react-router-dom";
import Sale, { NewSale } from "../lvl2/Sale";
import { ActionLink } from "../lvl1/Button";
import Page from "../lvl1/Page";
import { bytesToNumber } from "../../utils";
import { utils } from "ethers";
import useWatchRzREvent from '../../hooks/useWatchRzREvent'
import rzr, { SaleDetails} from "../../rzr";

const SalesContainer = styled.ul`
  display: flex;
  padding-left: 0;
  flex-wrap: wrap;
  justify-content: center;

  @media screen and (min-width: 720px) {
    justify-content: flex-start;
  }
`;

// only show on small screens when enabled
const NewSaleContainer = styled.div`
  margin-top: 3rem;
  display: flex;
  align-items: center;
  flex-direction: column;

  > p {
    margin-top: 0;
    max-width: 30rem;
    font-size: 14px;
  }
`;

const Market = () => {
  const location = useLocation();
  const isAdding = location.pathname.includes("new");
  const [sales, setSales] = useWatchRzREvent<'salesChanged'>('salesChanged', [])

  useEffect(() => {
    rzr.loadSales().then(setSales)
  }, []);

  return (
    <Page
      title={`Market ${isAdding ? " > sell" : ""}`}
      cta={
        isAdding ? (
          <ActionLink to="/market">Back</ActionLink>
        ) : (
          <ActionLink to="/market/new" $primary>
            Sell
          </ActionLink>
        )
      }
    >
      {isAdding ? (
        <>
          <p>Sell shards you no longer need.</p>
          <p>
            Creating a sale does not require you to submit a transaction but you
            need to sign each individual sale.
          </p>
          <NewSaleContainer>
            <NewSale />
            <p>
              * Any amount: Unchecking this option will make sure that buyers
              can only purchase the specified amount once, and that the sale
              cannot be composed with other sales you have.
            </p>
            <p>
              This option protects you from selling shards unwillingly but might
              deter buyers, as they are non-composable. Usually it is best to
              select this option for shards you are actively playing with.{" "}
            </p>
          </NewSaleContainer>
        </>
      ) : (
        <>
          <p>
            Tip: Purchasing more shards from the same seller are aggregated,
            decreasing transaction costs significantly.
          </p>
          <SalesContainer>
            {sales.map((sale) => (
              <Sale key={utils.hexlify(sale.signature)} sale={sale} />
            ))}
          </SalesContainer>
        </>
      )}
      {/*
      <ActionContainer>
        <ActionButton disabled>Checkout</ActionButton>
        </ActionContainer>*/}
    </Page>
  );
};

export default Market;
