import { useState, useContext } from "react";
import styled, {
  css,
  FlattenSimpleInterpolation,
  keyframes,
} from "styled-components";
import Asset, { styles, selectedStyles, disabledStyles } from "../lvl1/Asset";
import Board, { Element } from "../lvl2/Board";
import { StyledTriangle, triangleDimensions } from "../lvl1/Triangle";
import { ActionButton } from "../lvl1/Button";
import Page from "../lvl1/Page";
import ProgressBar, { ProgressContainer } from "../lvl1/ProgressBar";
import useSet from "../../hooks/useSet";
import useRzRShards from "../../hooks/useRzRShards";
import rzr, { Shard } from "../../rzr";
import Modal, { CloseIcon } from "../lvl1/Modal";
import { H2, H3 } from "../lvl1/Typography";
import AssetSelector from "../lvl2/AssetSelector";
import { getShardAsAsset, getShardIndex } from "../../utils";
import { BigNumber } from "ethers";
import { Input } from "../lvl1/Form";
import { Inline, Stack } from "../lvl1/Primitives";
import useModal from "../../hooks/useModal";

type FormElement = HTMLFormElement & {
  elements: {
    address: HTMLInputElement;
    url: HTMLInputElement;
  };
};

const NewRouterModal: React.FC<{ close: () => void }> = ({ close }) => {
  const onSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    rzr
      .storeRouter(
        (e.target as FormElement).elements.address.value,
        `ws://${(e.target as FormElement).elements.url.value}`
      )
      .then(close);
  };

  return (
    <Modal $size="sm" $justify="stretch">
      <Stack onSubmit={onSubmit} $justify="space-between" as="form" $expand>
        <Inline $justify="space-between">
          <H2 $font="secondary">New router</H2>
          <CloseIcon onClick={close} />
        </Inline>
        <Inline $justify="stretch" as="label">
          <span>Address</span>
          <Input name="address" />
        </Inline>
        <Inline $justify="stretch" as="label">
        <span>ws://</span>
          <Input name="url" />
        </Inline>
        <Inline $justify="flex-end">
          <ActionButton $primary>store</ActionButton>
        </Inline>
      </Stack>
    </Modal>
  );
};

export default NewRouterModal
