import styled, { css } from "styled-components";
import { Primitive } from "./types";

const Stack = styled.div<Primitive>`
  display: flex;
  flex-direction: column;
  justify-content: ${({ $justify }) => $justify || "auto"};
  ${({ $expand }) =>
    $expand
      ? css`
          flex-grow: 1;
        `
      : ""}

  > *:not(:first-of-type) {
    margin-top: ${({ $gap }) =>
      $gap === "sm"
        ? "0.25rem"
        : $gap === "md"
        ? "0.5rem"
        : $gap === "lg"
        ? "1rem"
        : "0"};
  }
`;

export default Stack;
