import { ActionButton } from "../../lvl1/Button";
import rzr from "../../../rzr";
import Modal, { CloseIcon } from "../../lvl1/Modal";
import { H2 } from "../../lvl1/Typography";
import { Input } from "../../lvl1/Form";
import { Inline, Stack } from "../../lvl1/Primitives";

type FormElement = HTMLFormElement & {
  elements: {
    identity: HTMLInputElement;
  };
};

const NewIdentityModal = () => {
  const onSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    rzr.storeIdentity(`0x${(e.target as FormElement).elements.identity.value}`);
  };

  return (
    <Modal $size="md" $justify="stretch">
      <Stack onSubmit={onSubmit} $justify="space-between" as="form" $expand>
        <Inline $justify="center">
          <H2 $font="secondary">Sign in with your identity</H2>
        </Inline>
        <Inline $justify="stretch" as="label">
          <span>0x</span>
          <Input name="identity" />
        </Inline>
        <Inline $justify="flex-end">
          <ActionButton $primary>sign in</ActionButton>
        </Inline>
      </Stack>
    </Modal>
  );
};

export default NewIdentityModal;
